import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';

import {connect} from 'react-redux';
import actions from '../../actions'; 

import {withStorytellerService} from '../hoc';

const ChangePassword = ({addErrorBanner, storytellerService}) => {
    const [oldPassword, changeOldPassword] = useState('');
    const [newPassword1, changeNewPassword1] = useState('');
    const [newPassword2, changeNewPassword2] = useState('');

    const [updateButtonEnable, setUpdateButtonEnable] = useState(false);

    const handleClick = () => {
        if (newPassword1 !== newPassword2) {
            addErrorBanner('  ', 3, 'white');
        }
        else {
            if (newPassword1.length < 4) {
                addErrorBanner('    5 .', 3.5, 'white');
            }
            else {
                storytellerService.users.updatePassword(oldPassword, newPassword1)
                .then(() => {
                    addErrorBanner('', 3, 'white');
                })
                .catch(() => {
                    addErrorBanner('    .    .', 3.5, 'white');
                })
            }
        }
    }

    useEffect(() => {
        if (oldPassword && newPassword2 && newPassword1) {
            setUpdateButtonEnable(true);
        }
        else {
            setUpdateButtonEnable(false);
        }
    }, [oldPassword, newPassword1, newPassword2])

    return (
        <div className="change-password-container__settings setting-inner">
            <div className="old-password small-data">
                <aside>
                     
                </aside>
                <input
                    type="password"
                    placeholder={'...'}
                    value={oldPassword}
                    onChange={({ target: { value } }) => changeOldPassword(value)} />
            </div>
            <div className="new-password1 small-data">
                <aside>
                     
                        </aside>
                <input type="password" placeholder={'...'} value={newPassword1} onChange={({ target: { value } }) => changeNewPassword1(value)} />
            </div>
            <div className="new2-password small-data">
                <aside>
                      
                </aside>
                <input type="password" placeholder={'...'} value={newPassword2} onChange={({ target: { value } }) => changeNewPassword2(value)} />
            </div>
            <div className="load-my-data small-data" >
                <div className={`blue-btn small ${updateButtonEnable ? '' : 'disabled'}`}>
                    <button onClick={handleClick}>
                        <span>
                            
                        </span>
                    </button>
                </div>
            </div>
        </div>
    )
}

export default connect(() => ({}), actions)(
    withStorytellerService()(ChangePassword)
)