import React from 'react';

import './style.scss';

import actions from '../../actions';
import { connect } from 'react-redux';

import { withStorytellerService } from '../hoc';
import {withRouter} from 'react-router-dom';

const Auth = () => (Wrapped) => connect(({nextRoute}) => ({nextRoute}), actions)
    (
        withRouter(
            withStorytellerService()((props) => {

                return (
                    <div className="auth">
                        <div className="left"></div>
                        <div className="right">
                            <Wrapped {...props}/>
                        </div>
                    </div>
                )
            })
        )
    )

export default Auth;