import React from 'react';
import ReactDOM from 'react-dom';

import './default-style/index.scss';

import './fonts/MinionPro.css';
import './fonts/PFBagueSansPro.css';

import { Provider as ReduxProvider } from 'react-redux';
import { BrowserRouter as Router } from 'react-router-dom';

import App from './components/app';
import ErrorBoundry from './components/error-boundry';
import StorytellerService from './services/storyteller';
import { StorytellerServiceProvider } from './components/context/storyteller-service';

import "current-device";

import store from './store';

const storytellerService = new StorytellerService();


ReactDOM.render(
    <ErrorBoundry>
        <ReduxProvider store={store}>
            <StorytellerServiceProvider value={storytellerService}>
                <Router>
                    <App />
                </Router>
            </StorytellerServiceProvider>
        </ReduxProvider>
    </ErrorBoundry>
    , document.getElementById('root'));