import React from 'react';
import MenuItem from './menu-item';

import './style.scss';
import { useEffect } from 'react';
import { useState } from 'react';
import { useCallback } from 'react';

const ContextMenu = ({ items, open, contextX, contextY, closeFunc: close }) => {

    const [myItemList, setMyItemList] = useState([]);
    const [reverse, EnableReverse] = useState(false);

    useEffect(() => {
        const newMyItemList = items.map(({ text, func, id, backgroundImage }) => {
            return (
                <MenuItem
                    key={id}
                    backgroundImage={backgroundImage}
                    func={func}
                    closeContextMenu={close}
                >
                    {text}
                </MenuItem>
            )
        })

        setMyItemList(newMyItemList);
    }, [items, close])

    const closeContextMenu = useCallback(() => {
        document.removeEventListener('scroll', closeContextMenu, false);
        document.removeEventListener('wheel', closeContextMenu, false);

        close();
    }, [close]);

    const handleClick = useCallback(({ path }) => {
        let con = true;

        for (let i = 0; i < path.length - 2; i++) {
            if (path[i].classList.contains('context-menu-container')) {
                con = false;
                break;
            }
        }

        if (con) {
            closeContextMenu();
            document.removeEventListener('mouseup', handleClick, false);
        };
    }, [closeContextMenu])

    useEffect(() => {
        if (open) {
            document.removeEventListener('mouseup', handleClick, false);
            document.addEventListener('mouseup', handleClick, false);

            document.removeEventListener('scroll', closeContextMenu, false);
            document.addEventListener('scroll', closeContextMenu, false);

            document.removeEventListener('wheel', closeContextMenu, false);
            document.addEventListener('wheel', closeContextMenu, false);
        }

        const deltaHeight = window.innerHeight - contextY;
        const contextMenuHeight = items.length * 30 + 30;

        if (deltaHeight <= contextMenuHeight) {
            EnableReverse(true);
        }
        else {
            EnableReverse(false);
        }

    }, [closeContextMenu, handleClick, open, contextY, items.length])


    return (
        <div className={`context-menu-container ${open ? 'open' : ''}`}
            style={{
                left: contextX,
                top: contextY
            }}
        >
            <div className={`context-menu-inner ${reverse ? 'reverse': ''}`}>
                {myItemList}
            </div>
        </div>
    )
}

export default ContextMenu;