import React from 'react';
import './style.scss';

import LinkButton from '../link-button';

import PushStoryBtn from '../push-story';

import { Route, Switch, Link } from 'react-router-dom';
import { connect } from 'react-redux';

import UserAvatar from '../user-avatar';

import SettingsPanel from '../settings-panel';
import { useState } from 'react';
import { useEffect } from 'react';

const UserPanel = ({ needLogin, userData }) => {

    const { username, urlToImage } = userData;
    const [redirectToDefaultSettings, setRedirectToDefaultSettings] = useState(false);
    const [settingsPanelVisible, setSettingPanelVisible] = useState(false);

    const hideSettingsPanel = () => {
        setSettingPanelVisible(false);
    }

    useEffect(() => {
        if (document.documentElement.classList.contains('mobile')) {
            setRedirectToDefaultSettings(true);
        }
    }, [])

    if (!needLogin) {
        return (
            <div className="user-panel">
                {/* <LinkButton linkTo="#" className="notifications up-item a" /> */}
                <SettingsPanel visible={settingsPanelVisible} hideMe={hideSettingsPanel} />
                <Switch>
                    <Route path='/write-story'>
                        <PushStoryBtn />
                    </Route>
                    <Route path='/settings'>
                        <LinkButton className="blue-btn minimize a" linkTo="/write-story" text=" " />
                    </Route>
                    <Route path='/'>
                        {
                            redirectToDefaultSettings ?
                                <LinkButton className="setting up-item a" linkTo='/settings/profile' /> :
                                <div className="setting up-item a" onClick={() => setSettingPanelVisible(true)}>
                                    <button>
                                        <span></span>
                                    </button>
                                </div>
                        }
                        <LinkButton className="blue-btn minimize a" linkTo="/write-story" text=" " />
                    </Route>
                </Switch>
                <UserAvatar urlToImage={urlToImage} className='user-avatar up-item' userId={username} />
            </div>
        )
    }
    else {
        return (
            <div className="user-panel">
                <Switch>
                    <Route path='/about'>
                        <LinkButton text='' linkTo='/registration' className='blue-btn' />
                    </Route>
                    <Route path='/registration'>
                        {/* <LinkButton text='' linkTo='/login' className='blue-btn' /> */}
                    </Route>
                    <Route path='/login'>
                        {/* <LinkButton text='' linkTo='/registration' className='blue-btn' /> */}
                    </Route>
                    <Route path='/'>
                        <Link to='/login'>
                            
                        </Link>
                        <LinkButton text='' linkTo='/registration' className='blue-btn' />
                    </Route>
                </Switch>
            </div>
        )
    }
}

export default connect(({ needLogin, userData }) => ({ needLogin, userData }), () => ({}))(
    UserPanel
)