import React from 'react';
import { useEffect } from 'react';

import StoryPreview from '../story-preview';
import { useState } from 'react';

import './stories-list.scss';

const StoriesList = ({ storiesList }) => {
    const [stories, setStories] = useState([]);

    useEffect(() => {
        const stories = storiesList.map(story => {
            const { publishedAt, views, comments, likes, _id, previewImage, previewTitle, previewBody } = story;

            return (
                <div className='story-preview__container' key={_id}>
                    <StoryPreview
                        id={_id}
                        likes={likes.length}
                        comments={comments.length}
                        views={views}
                        body={previewBody || '...'}
                        title={previewTitle}
                        publishedAt={publishedAt}
                        urlToImage={previewImage}
                    />
                </div>
            )
        })

        setStories(stories);
    }, [storiesList])

    return (
        <div className='stories-container' >
            {
                stories
            }
        </div>
    )
}

export default StoriesList;