import React from 'react';
import Input from '../input';

import { withStorytellerService } from '../hoc';

import { Link } from 'react-router-dom';

import './comment-style.scss';

import { connect } from 'react-redux';
import UserAvatar from '../user-avatar';

import actions from '../../actions';

class MyComment extends React.Component {
    state = {
        comment: ''
    }

    handleChange = ({ target: { value: comment } }) => this.setState({ comment });

    handleComment = () => {
        const { comment } = this.state;
        const { storyId, storytellerService, loadComments, addErrorBanner } = this.props;

        if (comment.length > 0) {
            storytellerService.stories.comment(storyId, comment).then(() => {
                this.setState({
                    comment: ''
                })
                loadComments();
            })
                .catch(() => {
                    addErrorBanner('     ', 3);
                })
        }
        else {
            addErrorBanner('    ', 5, 'white');
        }
    }

    render = () => {
        const { comment } = this.state;
        const { name, surname, username, urlToImage } = this.props.userData;

        let nameBlock = null;

        if (name && surname) {
            nameBlock = (
                <div className="name-and-surname">
                    <span>{name} </span><span>{surname}</span>
                </div>
            );
        }

        return (
            <div className='comment-container'>
                <UserAvatar className='user-avatar' userId={username} urlToImage={urlToImage} />
                <div className='comment-inner'>
                    <div className='author-info'>
                        <div className="names">
                            {nameBlock}
                            <div className="username">
                                <Link to={`/authors/${username}`}>
                                    <span>@{username}</span>
                                </Link>
                            </div>
                        </div>
                    </div>
                    <Input className='my-comment' value={comment} placeholder=" " onChange={this.handleChange} disabledAutoFocus />
                    <div className='blue-btn little-blue-btn'>
                        <button onClick={this.handleComment}>
                            <span>
                                
                            </span>
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}

export default connect(({ userData }) => ({ userData }), actions)(
    withStorytellerService()(MyComment)
);