import React from 'react';

import { connect } from 'react-redux';
import actions from '../../actions';

import { withStorytellerService } from '../hoc';
import { withRouter } from 'react-router-dom';

import { SimpleLoader } from '../loader';

const StoryBlock = ({ id, title, publishedAt: date, removeMe, storyClick }) => {

    return (
        <div className='my-story'>
            <div className="left">
                <div className="title" onClick={() => storyClick(id)}>
                    <h3>
                        {title}
                    </h3>
                </div>
                <div className="publishedAt">
                    {
                        date ? (
                            <React.Fragment>
                                <span>{date.day} </span>
                                <span>{date.month} </span>
                                <span>{date.year}</span>
                            </React.Fragment>
                        ) : null
                    }
                </div>
            </div>
            <div className="right">
                <button onClick={() => removeMe(id)} className='basket menu-item'></button>
            </div>
        </div>
    )
}

const NoResultH1 = () => {
    return (
        <h3 className='find-nothing'>
            
        </h3>
    )
}

class MyStories extends React.Component {
    state = {
        myStories: [],
        loading: true
    }

    componentDidMount = () => {
        this.createMyStoriesList();
    }

    createMyStoriesList = () => {
        const { userData, storytellerService } = this.props;

        const { userId } = userData;

        this.setState({
            myStories: [],
            loading: true
        })

        if (userId) {
            storytellerService.stories.getAll({
                find: {
                    userId
                },
                selection: {
                    previewTitle: true,
                    _id: true,
                    publishedAt: true
                }
            })
                .then(storiesList => {
                    this.buildStoriesList(storiesList)

                    this.setState({
                        loading: false
                    })
                })
                .catch(() => {
                    console.error(404)

                    this.setState({
                        loading: false
                    })
                })
        }
    }

    buildStoriesList = (storiesList) => {
        const myStories = storiesList.map(story => {
            const { _id: id, publishedAt, previewTitle: title } = story;

            return (
                <StoryBlock key={id} title={title} id={id} removeMe={this.removeStory} publishedAt={publishedAt} storyClick={this.storyClick}/>
            )
        })

        this.setState({
            myStories
        })
    }

    storyClick = (id) => {
        const {history} = this.props;

        history.push(`/stories/${id}`);
    }

    removeStory = (id) => {
        const {storytellerService, addErrorBanner} = this.props;

        storytellerService.stories.removeStory(id)
            .then(() => {
                addErrorBanner('  ');
                this.createMyStoriesList();
            })
            .catch(() => {
                addErrorBanner(' - ', 3);
            })
    }

    render() {
        const { myStories, loading } = this.state;

        let showNothing = false;

        if (!loading && myStories.length === 0) {
            showNothing = true;
        }

        return (
            <div className='my-stories setting-inner'>
                {
                    loading ? <SimpleLoader /> :
                        showNothing ?
                            <NoResultH1 /> :
                            myStories
                }
            </div>
        )
    }
}

export default connect(({ userData }) => ({ userData }), actions)(
    withStorytellerService()(
        withRouter(MyStories)
    )
);