import React from 'react';

import { useState } from 'react';
import { useEffect } from 'react';

import './style.scss';

import {connect} from 'react-redux';
import actions from '../../actions';

import ErrorBanner from './banner';
import { useCallback } from 'react';

const ErrorBannerConteiner = ({errorBanners, removeErrorBanner}) => {

    const [errorBannersList, setErrorBannerList] = useState([]);
    
    const createErrorBannerList = useCallback(() => {
        const errorBannersList = errorBanners.map(({message, delayTime: defaultDelayTime, id, style}, bannerId) => {

            let delayTime = defaultDelayTime;

            if (bannerId > 0) {
                delayTime = 0;
            }

            return (
                <ErrorBanner
                    key={id}
                    delayTime={delayTime}
                    removeMe={removeErrorBanner}
                    id={id}
                    styleClass={style}
                >
                    {message}
                </ErrorBanner>
            )
        })

        setErrorBannerList(errorBannersList);
    }, [errorBanners, removeErrorBanner])

    useEffect(() => {
        createErrorBannerList();
    }, [createErrorBannerList]);

    return (
        <div className='error-banner-container'>
            {errorBannersList}
        </div>
    )
}

const mapStateToProps = (state) => {
    const {errorBanners} = state;

    return {
        errorBanners
    }
}


export default connect(mapStateToProps, actions)(
    ErrorBannerConteiner
)