const login = (needLogin) => ({
    type: 'login',
    payload: needLogin
})

const setFirstStoryGenresList = (genre) => ({
    type: 'setFirstStoryGenresList',
    payload: genre
})

const removeFirstStoryGenresList = () => ({
    type: 'removeFirstStoryGenresList'
})

const loadGenres = (genresList) => ({
    type: 'loadGenres',
    payload: genresList
})

const addErrorMessage = (message, time) => ({
    type: 'addErrorMessage',
    payload: {
        message,
        time
    }
})

const updateUserData = (userData) => ({
    type: 'updateUserData',
    payload: userData
})

const setNextRoute = (nextRoute) => ({
    type: 'setNextRoute',
    payload: nextRoute
})

const addErrorBanner = (message, delayTime = 3, style='black') => {

    return {
        type: 'addErrorBanner',
        payload: {
            message,
            delayTime,
            style
        }
    }
}

const removeErrorBanner = (id) => {
    return {
        type: 'removeErrorBanner',
        payload: id
    }
}

export default {
    login,
    setFirstStoryGenresList,
    loadGenres,
    addErrorMessage,
    updateUserData,
    removeFirstStoryGenresList,
    setNextRoute,
    addErrorBanner,
    removeErrorBanner
}