import React from 'react';
import './style.scss';
import { connect } from 'react-redux';
import { withStorytellerService } from '../hoc';
import actions from '../../actions';

import LinkBtn from '../link-button';
import { withRouter } from 'react-router-dom';

import Input from '../input';

import Genres from './genres';

class LastChange extends React.Component {
    state = {
        previewTitle: '',
        previewImage: null,
        previewBody: '',

        usedGenres: [],

        loadDefaultGenre: true,
        needDefaultTitleAndBody: true,

        btnEnable: false
    }

    postStory = () => {
        const { usedGenres: genres, previewImage, previewBody, previewTitle } = this.state;

        const { title, body, storytellerService } = this.props;

        storytellerService.stories.post(title, body, genres, previewImage, previewBody, previewTitle).then(storyId => {
            const { history } = this.props;

            history.push(`/stories/${storyId}`);
        })
            .catch(() => { });
    }

    forLater = () => {
        const { storytellerService, title, body, addErrorBanner, history, userData: { username } } = this.props;
        const { previewTitle, previewBody, previewImage } = this.state;

        storytellerService.drafts.save(
            title,
            body,
            previewTitle,
            previewBody,
            previewImage
        )
            .then(() => {
                addErrorBanner('', 3);

                history.push(`/authors/${username}`);
            })
            .catch(() => {
                addErrorBanner('   , -,      ', 3);
            });
    }

    createTitle = ({ target: { value } }, maxLenght) => {
        this.setState(() => {
            return {
                previewTitle: this.normalizePreviewText(value, maxLenght)
            }
        })
    }

    createBody = ({ target: { value } }, maxLenght) => {
        this.setState(() => {
            return {
                previewBody: this.normalizePreviewText(value, maxLenght)
            }
        })
    }

    onMaxLength = (value, maxLenght) => {
        if (value.length > maxLenght) {
            return value.split('').slice(0, maxLenght).join('');
        }
        else {
            return value;
        }
    }

    normalizePreviewText = (value, maxLenght) => {
        const str = value.split('<br>').join('').split('<div>').join('').split('</div>').join('').replace('&nbsp;', ' ').replace('&nbs;', '').replace('&amp;', '');
        return this.onMaxLength(str, maxLenght);
    };

    handleImageFieldChange = evt => {
        const { storytellerService } = this.props;

        storytellerService.image.post(evt.target.files[0])
            .then(urlToImage => {
                this.setState(() => {
                    return {
                        previewImage: storytellerService.image.get(urlToImage)
                    }
                })
            })
            .catch(() => { });

        evt.target.value = null;
    }

    removeImage = () => {
        this.setState(() => {
            return {
                previewImage: null
            }
        })
    }

    removeLastUsedGenre = () => {
        const { usedGenres } = this.state;
        let newArr = usedGenres.slice(0, usedGenres.length - 1);
        newArr.splice(newArr.length, 1);

        this.setState({
            usedGenres: newArr
        })
    }

    addGenre = (genre) => {

        const { usedGenres } = this.state;


        const idx = usedGenres.findIndex(g => g === genre);

        if (idx !== -1) {
            this.removeGenre(genre);
        }
        else {

            this.setState({
                usedGenres: [
                    ...usedGenres,
                    genre
                ]
            })
        }
    }

    removeGenre = (genre) => {
        this.setState(({ usedGenres }) => {
            const id = usedGenres.findIndex(g => g === genre);

            if (id === -1) {
                return {
                    usedGenres
                }
            }
            else {
                let newArr = usedGenres.slice(0, usedGenres.length);
                newArr.splice(id, 1);

                return {
                    usedGenres: newArr
                }
            }
        })
    }

    componentDidMount = () => {
        const { firstStoryGenresList, removeFirstStoryGenresList } = this.props;

        this.setState({
            usedGenres: firstStoryGenresList
        })

        removeFirstStoryGenresList();
    }

    componentDidUpdate = ({ className: prevC }) => {
        const { previewBody, previewTitle, btnEnable, needDefaultTitleAndBody } = this.state;
        const { title, body, className } = this.props;

        if (needDefaultTitleAndBody) {
            if (className !== prevC) {
                const newTitle = title.split('<')[0].split(' ').slice(0, 9).join(' ');
                const newBody = body.length !== 0 ? body[0].value.split('<')[0].split(' ').slice(0, 9).join(' ') : '';

                this.setState({
                    needDefaultTitleAndBody: false,
                    previewTitle: newTitle,
                    previewBody: newBody
                })
            }
        }

        if (title.length !== 0 && body.length !== 0 && previewBody.length !== 0 && previewTitle.length !== 0) {

            if (!btnEnable) {
                this.setState({
                    btnEnable: true
                })
            }
        }
        else {

            if (btnEnable) {
                this.setState({
                    btnEnable: false
                })
            }
        }
    }

    handlePostStrory = () => {
        const { btnEnable } = this.state;
        if (btnEnable) {
            this.postStory();
        }
        else {
            const { addErrorBanner } = this.props;

            addErrorBanner('   , , -   ', 6, 'white');
        }
    }

    render() {
        const { previewBody, previewTitle, previewImage, usedGenres, btnEnable } = this.state;
        const { remove, genresList, className } = this.props;

        return (
            <div className={`last-story-change ` + className}>
                <div className="background" onClick={() => remove()}></div>
                <div className="layout">
                    <div className="col preview__container">
                        <h3> </h3>
                        <div className="preview">
                            <div className={`preview-image ${previewImage ? 'with-image' : ''}`} style={{
                                backgroundImage: `url(${previewImage})`
                            }}>
                                {previewImage ? <div className='cross' onClick={() => this.removeImage()}></div> : null}
                                <label>
                                    <input
                                        type="file"
                                        accept=".jpg, .jpeg, .png"
                                        name="upload-image"
                                        onChange={this.handleImageFieldChange}
                                    />
                                    
                                </label>
                            </div>
                            <div className="title-and-body__preview">
                                <Input tagName='h3' className='title' onChange={evt => this.createTitle(evt, 60)} placeholder='... (60 )' value={previewTitle} />
                                <Input tagName='p' className='body' onChange={evt => this.createBody(evt, 120)} placeholder=' ... (120 )' value={previewBody} />
                            </div>
                        </div>
                    </div>
                    <div className="col right-col">
                        <h3 className='add-genre-h3'>  </h3>
                        <Genres
                            removeLastUsedGenre={this.removeLastUsedGenre}
                            usedGenres={usedGenres}
                            genresList={genresList}
                            addGenre={this.addGenre}
                            removeGenre={this.removeGenre}
                        />
                        <div className="hr"></div>
                        <div className="publish">
                            <LinkBtn
                                linkTo='#'
                                onClick={this.handlePostStrory}
                                className={`blue-btn small-blue-btn ${btnEnable ? 'enabled' : 'disabled'}`}
                                text=''
                            />
                            {/* <LinkBtn linkTo='#' onClick={this.forLater} className='for-later-btn' text='  ' /> */}
                        </div>
                    </div>
                    <div className='close-me' onClick={remove}>
                        <div className='cross'>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default connect(({ genresList, firstStoryGenresList, userData }) =>
    (
        { genresList, firstStoryGenresList, userData }
    ), actions)(
        withStorytellerService()(
            withRouter(LastChange)
        )
    )