import React from 'react';

export default class StoriesBlock extends React.Component {
    storiesBlock = null;

    state = {
        mobility: false,
        width: '100%',
        posY: 0,
        posX: 0
    }

    onMouseDown = evt => {
        const parent = this.storiesBlock.getBoundingClientRect();
        const element = evt.target.getBoundingClientRect();

        document.body.style.overflowY = 'hidden';

        this.setState({
            mobility: true,
            width: parent.width,
            posY: element.y,
            posX: element.x
        })
        document.addEventListener('mousemove', this.mouseMove, false);
    }

    onMouseUp = evt => {
        const { mobility } = this.state;

        if (mobility) {
            document.body.style.overflowY = 'auto';

            const {moveField} = this.props; 
            let mobileId, secondId, mobilePos;

            const storiesBlocks = Array.prototype.slice.call(document.querySelectorAll('.body .stories-block'));

            secondId = storiesBlocks.map((block, id) => {
                const { y } = block.getBoundingClientRect();

                if (block.classList.contains('mobile')) {
                    mobileId = id;
                    mobilePos = y;
                }
                return y;
            }).sort((a, b) => a - b).findIndex(posY => posY === mobilePos);
            
            moveField(mobileId, secondId);

            this.setState({
                mobility: false
            })
            document.removeEventListener('mousemove', this.mouseMove, false);
        }
    }

    mouseMove = evt => this.setState({
        posY: evt.clientY,
        posX: evt.clientX
    })

    componentDidMount() {
        document.addEventListener('mouseup', this.onMouseUp, false);
    }
    componentWillUnmount() {
        document.removeEventListener("mouseup", this.onMouseUp, false);
        document.removeEventListener('mousedown', this.onMouseDown, false);
        document.removeEventListener('mousemove', this.mouseMove, false);
    }

    render = () => {
        const { children, className='', onMouseEnter, onMouseLeave, id, deleteField } = this.props;
        const { mobility, width, posY, posX } = this.state;

        return (
            <div
                className={`stories-block ${className} ${mobility ? 'mobile' : ''}`}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
                onMouseUp={this.onMouseUp}
                onTouchEnd={this.onMouseUp}
                style={
                    {
                        width: mobility ? width : '100%',
                        // height: mobility ? 240 : 'auto',
                        top: mobility ? posY - 16 : 'auto',
                        left: mobility ? posX-width+48 : 'auto'
                    }
                }
                ref={ref => this.storiesBlock = ref}
            >
                {children}
                <div className="menu">
                    <button
                        className="move menu-item"
                        onMouseDown={this.onMouseDown}
                    ></button>
                    <button className="basket menu-item" onClick={() => deleteField(id)}></button>
                </div>
            </div>
        )
    }
}