import React from 'react';
import StoriesBlock from '../stories-block';

const Distributor = ({deleteField, moveField, id}) => {
    return (
        <StoriesBlock deleteField={deleteField} moveField={moveField} className="show-menu distributor-menu" id={id}>
            <div className='distributor  story-item'></div>
        </StoriesBlock>
    )
}

export default Distributor;