import React from 'react';
// import { withRouter } from 'react-router-dom';
import { useHistory } from 'react-router-dom';

import './style.scss';

import defaultImage from './img/default-image.png';

const StoryPreview = ({ title, body, urlToImage, publishedAt: date, likes, comments, views, id, className = '' }) => {
    let history = useHistory();

    const handleClick = () => {
        history.push('/stories/' + id);
    }

    return (
        <div className={`story-preview ${className}`} onClick={handleClick}>
            <div className='img'>
                <img src={urlToImage || defaultImage} alt="" />
            </div>
            <div className='body'>
                <div className="main-info">
                    <h3>{title}</h3>
                    <p>{body}</p>
                </div>
                <div className="bottom-group">
                    <div className="left">
                        <div className="likes num"><span>{likes}</span></div>
                        <div className="comments num"><span>{comments}</span></div>
                        <div className="views num"><span>{views}</span></div>
                    </div>
                    <div className="right">
                        <div className="publishedAt">
                            {
                                date ? (
                                    <React.Fragment>
                                        <span>{date.day} </span>
                                        <span>{date.month} </span>
                                        <span>{date.year}</span>
                                    </React.Fragment>
                                ) : null
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default StoryPreview;