import React from 'react';
import AddImageBtn from './add-image';

export default ({ addField }) => {
    return (
        <div className='add-field-buttons-group'>
            <button
                className='add-field add-text-field'
                onClick={() => addField('text')}
            >
                <span></span>
            </button>
            <button
                className='add-field add-quote-field'
                onClick={() => addField('quote')}
            >
                <span></span>
            </button>
            <AddImageBtn className='add-field add-img-field' onClick={addField}>
                
            </AddImageBtn>

            <button
                className='add-field add-distributor'
                onClick={() => addField('distributor')}
            >
                <span></span>
            </button>
        </div>
    )
}