import React from 'react';
import StoriesBlock from '../stories-block';

const ImageField = ({ deleteField, moveField, urlToImage, id }) => {

    return (
        <StoriesBlock deleteField={deleteField} moveField={moveField} id={id} className="show-menu with-border stretchs">
            <img src={urlToImage} alt='' className='image-field story-item img'/>
        </StoriesBlock>
    )
}

export default ImageField;