import React, { useEffect, useState, useRef, useCallback } from 'react';

const ErrorBanner = ({ children, delayTime, removeMe, id, styleClass }) => {
    const [showMe, setShowMe] = useState(false);

    const dTimerId = useRef(null);

    const removeBanner = useCallback(() => {
        clearTimeout(dTimerId.current);
        setShowMe(false);

        setTimeout(() => {
            removeMe(id)
        }, 400)
    }, [id, removeMe])

    useEffect(() => {
        setTimeout(() => {
            setShowMe(true);
        }, 400)
    }, []);

    useEffect(() => {
        clearTimeout(dTimerId.current);
        dTimerId.current = setTimeout(removeBanner, delayTime * 1000);
    }, [delayTime, removeBanner])

    return (
        <div className={`error-banner ${showMe ? '' : 'hide'} ${styleClass}`} onClick={removeBanner}>
            <span>
                {children}
            </span>
        </div>
    )
}

export default ErrorBanner;