import React from 'react';
import { withStorytellerService } from '../../hoc';

const ImageField = ({ children, onClick, className, storytellerService }) => {

    const handleChange = evt => {
        storytellerService.image.post(evt.target.files[0])
            .then(urlToImage => {
                onClick(`img ${storytellerService.image.get(urlToImage)}`);
            })
            .catch(() => { });

        evt.target.value = null;
    }

    return (
        <label className={className}>
            <input
                type="file"
                onChange={handleChange}
                accept=".jpg, .jpeg, .png"
                name="upload-image"
            />
            <span>
                {children}
            </span>
        </label>
    )
}

export default withStorytellerService()(ImageField)