import React from 'react';

import './comment-style.scss';

import StoryBlock from './story-block';

import { Link, withRouter } from 'react-router-dom';
import UserAvatar from '../user-avatar';
import { withStorytellerService } from '../hoc';

import { connect } from 'react-redux';
import actions from '../../actions';

import ContextMenu from '../context-menu';

class Comment extends React.Component {

    loadMyLike = true;
    loadMyComment = true;
    bigCommentBlock = React.createRef();

    state = {
        urlToImage: null,
        surname: null,
        username: null,
        name: null,
        storyId: null,
        commentId: null,
        likes: [],

        showComment: false,
        bigComment: false,

        liked: false,
        myComment: false,

        openContextMenu: false,
        contextX: 0,
        contextY: 0
    }

    componentDidUpdate = () => {

        const { likes } = this.state;
        const { userData, userId: commentUserId, body: comment } = this.props;
        const { userId } = userData;


        if (this.loadMyLike) {
            if (userId) {
                const myLikeId = likes.findIndex(like => like.userId === userId);

                if (myLikeId !== -1) {
                    this.setState({
                        liked: true
                    })
                }

                if (commentUserId === userId) {
                    this.setState({
                        myComment: true
                    })
                }

                this.loadMyLike = false;
            }
        }
        if (this.loadMyComment && comment) {
            if (this.bigCommentBlock.current.el.current.clientHeight > 200) {
                this.setState({
                    bigComment: true
                })
            }

            this.loadMyComment = false;
        }
    }

    likeComment = () => {
        this.redirectToLoginFromUnauth(() => {
            const { storytellerService, storyId, id } = this.props;
            const { liked } = this.state;

            let likeFunc = storytellerService.stories;

            if (liked) {
                likeFunc = likeFunc.commentUnlike;
            }
            else {
                likeFunc = likeFunc.commentLike;
            }

            likeFunc(storyId, id)
                .then(() => {

                    this.setState(({ liked, likes }) => {
                        let newLikesList = likes;
                        
                        if (!liked) {
                            newLikesList.push({ob: 1});
                        }
                        else {
                            newLikesList.pop(0);
                        }

                        return {
                            liked: !liked,
                            likes: newLikesList
                        }
                    });
                })
                .catch(this.serverError)
        });
    }

    //todo: repair
    loadLikes = () => {
        const { storytellerService, id, storyId } = this.props;

        const selection = {
            "likes": true
        }

        const find = {
            "comments._id": id
        }

        storytellerService.stories.getSmallData(storyId, selection, find)
            .then(({ likes }) => {
                console.log(likes);
                this.setState({ likes });
            })
            .catch(this.serverError)
    }

    componentDidMount = () => {
        this.loadComment();
    }

    loadComment = () => {
        const { storytellerService, userId, likes } = this.props;

        this.setState({
            likes
        })

        const selection = {
            username: true,
            name: true,
            surname: true,
            urlToImage: true
        }

        storytellerService.users.getById(userId, selection)
            .then(({ username, surname, name, urlToImage }) => {
                this.setState({
                    username,
                    surname,
                    name,
                    urlToImage
                })
            })
            .catch(() => {
                this.setState({
                    username: '',
                    name: ' '
                })
            })
    }

    openAllComment = () => {
        const { bigComment } = this.state;

        if (bigComment) {

            this.setState(({ showComment }) => {
                return {
                    showComment: !showComment
                }
            })
        }
    }

    openAllCommentFromCommentText = () => {
        const { showComment } = this.state;

        if (showComment) {
            this.openAllComment();
        }
    }

    openContextMenu = ({ clientX, clientY }) => {
        this.setState({
            openContextMenu: true,
            contextX: clientX,
            contextY: clientY
        })
    }


    closeContextMenu = () => {
        this.setState({
            openContextMenu: false
        })
    }

    complainComment = () => this.redirectToLoginFromUnauth(() => {
        const { addErrorBanner } = this.props;

        addErrorBanner('  ', 3);
    })

    removeComment = () => {
        this.redirectToLoginFromUnauth(
            () => {
                const { storyId, id, storytellerService, updateCommentsList } = this.props;

                storytellerService.stories.removeComment(storyId, id)
                    .then(() => {
                        updateCommentsList();
                    })
                    .catch(this.serverError);
            }
        )
    }

    redirectToLoginFromUnauth = (FIRST, SECOND) => {
        const { needLogin, location: { pathname }, history, setNextRoute } = this.props;

        if (needLogin) {
            setNextRoute(pathname);
            history.push('/registration');

            if (SECOND) {
                SECOND();
            }
        }
        else {
            FIRST();
        }
    }

    serverError = (error) => {
        const { addErrorBanner } = this.props;

        console.log(error);
        addErrorBanner('     ', 3);
    }

    render = () => {
        const {
            username,
            name,
            surname,
            urlToImage,
            likes,
            liked,
            myComment,
            showComment,
            bigComment,
            openContextMenu,
            contextX,
            contextY
        } = this.state;

        const {
            body = '',
            publishedAt: date
        } = this.props;

        let nameBlock = null;

        let contextMenu = [];

        if (myComment) {
            contextMenu.push({
                text: '',
                func: this.removeComment,
                id: 1
            });
        }
        else {
            contextMenu.push({
                text: '',
                func: this.complainComment,
                id: 0
            });
        }

        if (name) {
            nameBlock = (
                <div className="name-and-surname">
                    <span>{name}</span><span> {surname}</span>
                </div>
            )
        }

        return (
            <div className='comment-container'>
                <UserAvatar className='user-avatar' userId={username} urlToImage={urlToImage} />
                <div className='comment-inner'>
                    <div className='author-info'>
                        <div className="names">
                            {nameBlock}
                            <div className="username">
                                <Link to={`/authors/${username}`}>
                                    <span>@{username}</span>
                                </Link>
                            </div>
                        </div>
                        <div className='comment-context-menu-open-btn' onClick={this.openContextMenu}>

                        </div>
                    </div>
                    <div
                        className={`${showComment ? 'open' : ''} ${bigComment ? '' : 'hidden'} user-comment`}
                        onClick={this.openAllCommentFromCommentText}
                    >
                        <StoryBlock
                            className={`comment-text`}
                            tagName='div'
                            _ref={this.bigCommentBlock}
                        >
                            {body}
                        </StoryBlock>
                        <div className={`all-comment-btn ${showComment ? 'open' : ''}`}
                            onClick={this.openAllComment}>
                            <span>
                                 
                        </span>
                        </div>
                    </div>
                    <div className="bottom">
                        <div className="left">
                            <div className={`likes num ${liked ? 'active' : ''}`} onClick={this.likeComment}>
                                <span>{likes.length.toLocaleString('en')}</span>
                            </div>
                        </div>
                        <div className="right">
                            <div className="publishedAt">
                                {
                                    date ? (
                                        <React.Fragment>
                                            <span>{date.day} </span>
                                            <span>{date.month} </span>
                                            <span>{date.year}</span>
                                        </React.Fragment>
                                    ) : null
                                }
                            </div>
                        </div>

                    </div>
                </div>
                <ContextMenu items={contextMenu} open={openContextMenu}
                    contextX={contextX}
                    contextY={contextY}
                    closeFunc={this.closeContextMenu}
                />
            </div>
        )
    }
}

export default connect(({ userData, needLogin }) => ({ userData, needLogin }), actions)(
    withStorytellerService()(
        withRouter(Comment)
    )
);