import React from 'react';
import Auth from '../auth';

import LinkButton from '../link-button';

import Input from '../input';

import { Link } from 'react-router-dom';

class Registration extends React.Component {
    state = {
        loginButtonEnable: false,

        username: '',
        password: '',
        email: '',
        usernameTimer: null,

        trueUser: false,
        trueEmail: false
        // ,
        // truePass: false
    }

    handleLogin = () => {
        const { storytellerService, login, updateUserData } = this.props;
        const { username, password, email } = this.state;

        storytellerService.reg(username, email, password)
            .then((userData) => {
                login(false);

                const { username, name, surname, urlToImage, _id: userId } = userData;


                updateUserData({
                    userId,
                    username,
                    name,
                    urlToImage,
                    surname
                })
            })
            .catch((e) => {
                login(true);

                const { addErrorBanner } = this.props;
                addErrorBanner('   ', 3);
            });
    }

    username = ({ target: { value } }) => {

        this.setState({
            username: this.correctUsername(value)
        })
    }

    email = ({ target: { value } }) => this.setState({
        email: this.correctEmail(value)
    })

    password = ({ target: { value } }) => {
        // const {addErrorBanner} = this.props;
        
        const newPassword = this.convertPassword(this.disableSpaceAndEnter(value));

        // let truePass = false;

        // if (newPassword.length > 4) {
        //     truePass = true;
        // }
        // else {
        //     addErrorBanner('    4 ', 3, 'white');
        // }

        this.setState({
            password: newPassword
        })
    }


    correctUsername = (value) => {
        const { addErrorBanner, storytellerService } = this.props;

        const firstCorrect = this.maxLength(
            this.disableSpaceAndEnter(value),
            16
        )

        storytellerService.checkUsername(firstCorrect)
            .then(code => {
                const clearTimer = () => {
                    const { usernameTimer } = this.state;
                    clearTimeout(usernameTimer);
                }

                const addTimer = (message, delayTime) => {
                    clearTimer();

                    this.setState({
                        usernameTimer: setTimeout(() => {
                            addErrorBanner(message, delayTime, 'white');
                        }, 500),
                        trueUser: false
                    })
                }

                if (value.length !== 0) {
                    switch (code) {
                        case 'busy':
                            addTimer('    ', 3);
                            break;

                        case 'incorrect':
                            addTimer('   4  16 .    ,     "_"', 3);
                            break;

                        default:
                            clearTimer();
                            this.setState({
                                trueUser: true
                            })
                    }
                }
            })
            .catch(() => {
                addErrorBanner('   , -, ', 3, 'white');
            })

        return firstCorrect;
    }

    correctEmail = (value) => {
        const { addErrorBanner, storytellerService } = this.props;

        const firstCorrect = this.disableSpaceAndEnter(value);

        storytellerService.checkEmail(firstCorrect)
            .then(code => {
                const clearTimer = () => {
                    const { usernameTimer } = this.state;
                    clearTimeout(usernameTimer);
                }

                const addTimer = (message, delayTime) => {
                    clearTimer();

                    this.setState({
                        usernameTimer: setTimeout(() => {
                            addErrorBanner(message, delayTime, 'white');
                        }, 2500),
                        trueEmail: false
                    })
                }

                if (value.length !== 0) {
                    switch (code) {
                        case 'busy':
                            addTimer('   ', 3);
                            break;

                        case 'incorrect':
                            addTimer('  ', 3);
                            break;

                        default:
                            clearTimer();
                            this.setState({
                                trueEmail: true
                            })
                    }
                }
            })
            .catch(() => {
                addErrorBanner('   , -, ', 3, 'white');
            })

        return firstCorrect;
    }


    maxLength = (value, maxLength) => {
        if (value.length > maxLength) {
            const { addErrorBanner } = this.props;
            addErrorBanner('   16 ', 5, 'white');

            return value.slice(0, maxLength);
        }

        return value;
    }

    disableSpaceAndEnter = (value) => {
        const removeSymbol = ['<br>', '<div>', '</div>', '</br>'];

        let newValue = value;

        removeSymbol.forEach(symbol => {
            newValue = newValue.split(symbol).join('');
        })

        return newValue.replace('&nbsp;', ' ').replace('&nbsp;', '');
    }

    convertPassword = (value) => {
        const { password } = this.state;

        const vLength = value.length;
        const pLength = password.length;

        let newPassword = password;

        if (vLength > pLength) {
            newPassword = password + value.slice(vLength - 1, vLength);
        }
        else {
            newPassword = password.slice(0, vLength);
        }

        return newPassword;
    }

    componentDidUpdate = () => {
        const { trueUser, password, loginButtonEnable, trueEmail } = this.state;

        if (trueEmail && trueUser && password) {
            if (!loginButtonEnable) {
                this.setState({
                    loginButtonEnable: true
                })
            }
        }
        else {
            if (loginButtonEnable) {
                this.setState({
                    loginButtonEnable: false
                })
            }
        }
    }

    componentDidMount = () => {
        document.title = '  Storyteller';
        document.addEventListener('keydown', this.onKeyDown, false); 
    }

    componentWillUnmount = () => {
        document.removeEventListener('keydown', this.onKeyDown, false); 
    }

    onKeyDown = ({key}) => {
        if (key === 'Enter') {
            this.EnterLogin();
        }
    }

    EnterLogin = () => {
        const {loginButtonEnable} = this.state;
        const {addErrorBanner} = this.props;

        if (loginButtonEnable) {
            this.handleLogin();
        }
        else {
            addErrorBanner('   ', 3, 'white');
        }
    }

    createPasswordDots = ({ length }) => {
        const dot = '';
        return dot.repeat(length);
    }

    render = () => {
        const { loginButtonEnable, username, email, password } = this.state;

        const passwordDots = this.createPasswordDots(password);

        return (
            <React.Fragment>
                <div className='p'>
                     !   ,
                    &#160;
                    <Input
                        placeholder=" "
                        value={username}
                        onChange={this.username}
                        className='input'
                        disabledAutoFocus
                    />
                    .&#160;     &#160;
                    <Input
                        placeholder=" "
                        value={email}
                        onChange={this.email}
                        className='input'
                        disabledAutoFocus
                    />
                    &#160;&#160;
                    <Input
                        placeholder=""
                        value={passwordDots}
                        onChange={this.password}
                        className='input'
                        disabledAutoFocus
                    />
                    .   ...
                </div>
                <div className="bottom">
                    <LinkButton
                        linkTo="#"
                        text=""
                        className={`blue-btn ${loginButtonEnable ? '' : 'disabled'}`}
                        onClick={this.handleLogin}
                    />
                    <Link to="/login">
                            
                    </Link>
                </div>
            </React.Fragment>
        )
    }
}

export default Auth()(Registration);