import React from 'react';
import UserAvatar from '../user-avatar';

import { useEffect, useState } from 'react';
import { connect } from 'react-redux';

import actions from '../../actions';

import { withStorytellerService } from '../hoc';
import { withRouter } from 'react-router-dom';

import { SimpleLoader } from '../loader';

const FollowerBlock = ({ id, follow, getUserById }) => {
    const [userData, setUserData] = useState({
        username: '',
        urlToImage: null
    })

    const { username, urlToImage } = userData;

    useEffect(() => {
        getUserById(id, {
            username: true,
            urlToImage: true,
            _id: false
        })
            .then(({ username, urlToImage }) => {
                setUserData({
                    username,
                    urlToImage
                })
            })
            .catch(() => {
                console.error(404);
            })
    }, [getUserById, id])

    return (
        <div className='following'>
            <div className="left">
                <UserAvatar urlToImage={urlToImage} className='user-avatar' />
            </div>
            <div className="right">
                <div className="top">
                    <span>
                        @{username}
                    </span>
                </div>
                <div className="bottom">
                    <button className='follow' onClick={() => follow(id)}>
                        
                    </button>
                </div>
            </div>
        </div>
    )
}

const NoResultH1 = () => {
    return (
        <h3 className='find-nothing'>
            
        </h3>
    )
}

class UserFollowers extends React.Component {
    state = {
        followingsList: [],
        loading: true
    }

    follow = (id) => {
        const { storytellerService, addErrorBanner } = this.props;

        storytellerService.users.unfollow(id)
            .then(() => {
                addErrorBanner('', 3, 'white');
                this.getMyFollowings();
            })
            .catch(() => {
                addErrorBanner('     ', 3, 'white');
            })
    }

    componentDidMount = () => {
        this.getMyFollowings();
    }

    getMyFollowings = () => {
        const { userData, storytellerService } = this.props;
        const { username } = userData;

        this.setState({
            loading: true,
            followingsList: []
        })

        if (username) {
            storytellerService.users.getSmallData(username, {
                followings: true
            })
                .then(({ followings }) => {
                    this.createFollowingsList(followings);

                    this.setState({
                        loading: false
                    })
                })
                .catch(() => {
                    console.error(404);

                    this.setState({
                        loading: false
                    })
                })
        }
    }

    createFollowingsList = (followings) => {
        const { storytellerService } = this.props;

        const followingsList = followings.map(user => {
            const { userId: id } = user;

            return (
                <FollowerBlock key={id} follow={this.follow} id={id} getUserById={storytellerService.users.getById} />
            )
        })

        this.setState({
            followingsList
        })
    }

    render() {
        const { followingsList, loading } = this.state;

        let showNothing = false;

        if (!loading && followingsList.length === 0) {
            showNothing = true;
        }

        return (
            <div className='my-followings setting-inner'>
                {
                    loading ? <SimpleLoader /> :
                        showNothing ?
                            <NoResultH1 /> :
                            followingsList
                }
            </div>
        )
    }
}

export default connect(({ userData }) => ({ userData }), actions)(
    withStorytellerService()(
        withRouter(UserFollowers)
    )
)