import React from 'react';
import UserAvatar from '../user-avatar';
import { Link, withRouter } from 'react-router-dom';
// import { useCallback } from 'react';

const UserBlock = ({ name, surname, urlToImage, username, followers, history }) => {

    // const handleClick = useCallback(() => {
    //     history.push(`/authors/${username}`);
    // }, [history, username])

    return (
        <div className='author-block'> 
            <UserAvatar className='user-avatar' userId={username} urlToImage={urlToImage} />
            <div className="right">
                <div className="user-names">
                    <div className="name-and-surname">
                        <span>
                            {name}
                        </span>
                        <span>
                            {surname}
                        </span>
                    </div>
                    <div className="username">
                        <Link to={`/authors/${username}`}>
                            <span>
                                @{username}
                            </span>
                        </Link>
                    </div>
                </div>
                <div className="followers-list">
                    <div className='logo'></div>
                    <div className='followers-list-inner'>
                        <span className='number'>
                            {followers.length.toLocaleString('en')}
                        </span>
                        <span>
                            
                        </span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(UserBlock);