import React from 'react';
import LinkButton from '../link-button';

export default ({ urlToImage, className, userId }) => {

    return (
        <LinkButton
            linkTo={`/authors/${userId}`}
            className={`${className} ${urlToImage ? '' : 'default'}`}
            style={
                urlToImage ? { backgroundImage: `url(${urlToImage})` } : {}
            }
        />
    )
}