const initialState = {
    needLogin: true,
    firstStoryGenresList: [],
    genresList: [],
    userData: {
        username: undefined,
        urlToImage: null,
        name: undefined,
        surname: undefined,
        userId: undefined,
        about: undefined,
        email: undefined
    },
    nextRoute: null,

    errorBanners: []
}

export default (state = initialState, { type, payload }) => {

    switch (type) {

        case 'login':
            return {
                ...state,
                needLogin: payload
            }

        case 'setFirstStoryGenresList':
            const genres = state.firstStoryGenresList;
            const id = genres.findIndex(genre => genre === payload);

            if (id !== -1) {
                genres.splice(id, 1);
            }
            else {
                genres.push(payload);
            }

            return {
                ...state,
                firstStoryGenresList: genres
            }

        case 'loadGenres':
            return {
                ...state,
                genresList: payload
            }

        case 'updateUserData':
            return {
                ...state,
                userData: {
                    ...state.userData,
                    ...payload
                }
            };

        case 'removeFirstStoryGenresList':
            return {
                ...state,
                firstStoryGenresList: []
            }

        case 'setNextRoute':
            return {
                ...state,
                nextRoute: payload
            }

        case 'addErrorBanner':
            const {errorBanners} = state;

            return {
                ...state,
                errorBanners: [
                    {
                        ...payload,
                        id: Date.now()
                    },
                    ...errorBanners.slice(0, errorBanners.length)
                ]
            }

        case 'removeErrorBanner':
            const {errorBanners: a} = state;

            const bannerId = a.findIndex(banner => banner.id === payload);

            if (bannerId !== -1) {
                return {
                    ...state,
                    errorBanners: [
                        ...a.slice(0, bannerId),
                        ...a.slice(bannerId+1, a.length)
                    ]
                }
            }

            return {
                ...state
            }

        default:
            return state
    }
}
