import React from 'react';
import './style.scss';

import UserPanel from '../user-panel';

import { Link } from 'react-router-dom';

const Header = () => {

    return (
        <header>
            <div className="top">
                <div className='left'>
                    <Link to="/">
                        <div className="storyteller-logo"></div>
                    </Link>
                </div>
                <div className='right'>
                    <UserPanel />
                </div>
            </div>
        </header>
    )
}

export default Header;