import React from 'react';

import Input from '../../../input';

import StoriesBlock from '../stories-block';

export default class TextField extends React.Component {

    state = {
        value: '',
        onFocus: false,
        onHover: false,
        showMenu: false
    }

    componentDidUpdate = () => {
        const { onFocus, onHover, showMenu } = this.state;

        if (!onFocus && onHover && !showMenu) {
            this.setState({
                showMenu: true
            })
        }

        if (onFocus && showMenu) {
            this.setState({
                showMenu: false
            })
        }
    }

    render = () => {
        const { value, showMenu } = this.state;
        const { id, deleteField, moveField } = this.props;

        return (
            <StoriesBlock
                className={`with-border ${showMenu ? 'show-menu' : ''} stretch`}
                onMouseEnter={() => this.setState({ onHover: true })}
                onMouseLeave={() => this.setState({ onHover: false })}
                id={id}
                deleteField={deleteField}
                moveField={moveField}
            >
                <Input
                    value={value}
                    className='text field story-item'
                    tagName='div'
                    placeholder='...'
                    onChange={evt => this.setState({ value: evt.target.value })}
                    onFocus={() => this.setState({ onFocus: true })}
                    onBlur={() => this.setState({ onFocus: false })}
                />
            </StoriesBlock>
        )
    }
}