export default class StoryService {
    // BASE_URL = "http://192.168.1.6:9000/api";
    // BASE_URL = "http://127.0.0.1:9000/api";
    BASE_URL = '/api';

    _setToken = (token) => localStorage.setItem('token', token)

    _getToken = () => localStorage.getItem('token')

    _sendReq = (url, body = {}, method = 'post', contentType = 'application/json') => fetch(this.BASE_URL + url, {
        method,
        headers: {
            "Content-Type": contentType,
            "Authorization": this._getToken()
        },
        body: JSON.stringify(body)
    })

    _post = (url, body) => fetch(this.BASE_URL + url, {
        method: 'post',
        headers: {
            "Authorization": this._getToken()
        },
        body
    })

    _get = (url) => fetch(this.BASE_URL + url);

    _checkError = (res) => {
        if (!res.ok) {
            throw res.status;
        }
    }

    _parseRes = async (res, type = "json") => {
        this._checkError(res);

        switch (type) {
            case 'json':
                return await res.json()
            case 'text':
                return await res.text()
            default:
                throw new Error("Invalid action type");
        }
    }

    auth = async () => {
        const res = await this._sendReq('/auth');

        if (!res.ok) {
            return {
                status: res.status
            }
        }
        else {
            return {
                ...(await this._parseRes(res)),
                status: res.status
            };
        }
    }

    login = async (username, password) => {
        const res = await this._sendReq('/login', {
            username,
            password
        });
        const json = await this._parseRes(res);
        this._setToken(
            json.token
        );
        delete json.token;

        return json;
    }

    logout = async () => {
        this._setToken('S T O R Y T E L L E R');
    }

    reg = async (username, email, password) => {
        const res = await this._sendReq('/reg', {
            username,
            email,
            password
        })
        const json = await this._parseRes(res);
        this._setToken(
            json.token
        );
        delete json.token;

        return json;
    }

    checkUsername = async (username) => {
        if (username.match(/^[A-Za-z0-9_]{4,16}$/)) {
            const res = await this._sendReq(`/users/${username}`, {
                _id: true
            });

            if (res.ok) {
                return 'busy'
            }
            else {
                return 'empty'
            }
        }
        else {
            return 'incorrect';
        }
    }

    checkEmail = async (email) => {
        if (email.match(/^([a-zA-Z0-9_\-.]+)@([a-zA-Z0-9_\-.]+)\.([a-zA-Z]{2,5})$/)) {
            const res = await this._sendReq(`/users/check-email/${email}`, {
                _id: true
            })

            if (res.ok) {
                return 'busy'
            }
            else {
                return 'empty'
            }
        }
        else {
            return 'incorrect';
        }
    }

    genres = {
        getAll: async () => {
            const res = await this._get('/genres/');

            return await this._parseRes(res);
        }
    }

    image = {
        get: (name) => {
            return `${this.BASE_URL}/images/${name}`;
        },
        post: async (fileData) => {
            let formData = new FormData();
            formData.append('filedata', fileData);

            const res = await this._post('/images/', formData);
            return await this._parseRes(res, 'text')
        }
    }

    // users = {
    //     get: async (id) => {
    //         const res = await this._get(`/users/${id}`);
    //         return await this._parseRes(res);
    //     }
    // }

    getMonth = (monthId) => {
        switch (monthId) {
            case 0:
                return ''
            case 1:
                return ''
            case 2:
                return ''
            case 3:
                return ''
            case 4:
                return ''
            case 5:
                return ''
            case 6:
                return ''
            case 7:
                return ''
            case 8:
                return ''
            case 9:
                return ''
            case 10:
                return ''
            case 11:
                return ''

            default: throw new Error('Invalid date')
        }
    }

    convertData = (d) => {
        const date = new Date(d);
        return {
            day: date.getDate(),
            month: this.getMonth(date.getMonth()),
            year: date.getFullYear()
        }
    }

    stories = {
        get: async (id) => {
            const res = await this._get('/stories/' + id);
            const story = await this._parseRes(res);

            const { publishedAt } = story;

            return {
                ...story,
                publishedAt: this.convertData(publishedAt)
            }
        },
        getSmallData: async (id, selection, find = {}) => {
            const res = await this._sendReq(`/stories/find-one`, {
                id,
                selection,
                find
            })

            return this._parseRes(res);
        },
        getAll: async (body) => {
            const res = await this._sendReq('/stories/find', body);
            const stories = await this._parseRes(res);

            return stories.map(story => {
                const { publishedAt } = story;

                return {
                    ...story,
                    publishedAt: this.convertData(publishedAt)
                }
            })
        },
        post: async (title, body, genres, previewImage, previewBody, previewTitle) => {
            const res = await this._sendReq('/stories/', {
                title,
                body,
                genres,
                previewImage,
                previewBody,
                previewTitle
            })

            return await this._parseRes(res);
        },
        like: async (id) => {
            const res = await this._sendReq(`/stories/like/${id}`)

            return this._checkError(res);
        },
        unlike: async (id) => {
            const res = await this._sendReq(`/stories/like/${id}`, {}, 'DELETE');

            return this._checkError(res);
        },

        removeStory: async (id) => {
            const res = await this._sendReq(`/stories/${id}`, {}, 'DELETE');

            return this._checkError(res);
        },

        comment: async (storyId, body) => {
            const res = await this._sendReq('/stories/comment/', {
                storyId,
                body
            })

            return this._checkError(res);
        },

        commentLike: async (storyId, commentId) => {
            const res = await this._sendReq('/stories/comment/like', {
                storyId,
                commentId
            });

            return await this._checkError(res);
        },

        commentUnlike: async (storyId, commentId) => {
            const res = await this._sendReq('/stories/comment/like', {
                storyId,
                commentId
            }, 'delete');

            return await this._checkError(res);
        },

        removeComment: async (storyId, commentId) => {
            const res = await this._sendReq('/stories/comment/', {
                storyId,
                commentId
            }, 'delete');

            return await this._checkError(res);
        }
    }

    drafts = {
        save: async (title, body, previewTitle, previewBody, previewImage) => {
            const res = await this._sendReq('/drafts/', {
                title,
                body,
                previewTitle,
                previewBody,
                previewImage
            })

            return await this._checkError(res);
        }
    }

    users = {
        get: async (username, selection) => {
            const res = await this._sendReq('/users/' + username, selection);
            return this._parseRes(res);
        },
        getById: async (id, selection) => {
            const res = await this._sendReq('/users/getById/' + id, selection);
            return this._parseRes(res);
        },
        getSmallData: async (id, selection) => {
            const res = await this._sendReq(`/users/get`, {
                id,
                selection
            })

            return this._parseRes(res);
        },
        follow: async (id) => {
            const res = await this._sendReq('/users/follow/' + id);

            return this._checkError(res);
        },
        unfollow: async (id) => {
            const res = await this._sendReq('/users/follow/' + id, {}, 'delete');

            return this._checkError(res);
        },
        getAll: async (skip, limit, selection, find) => {
            const res = await this._sendReq('/users/find', {
                selection,
                skip,
                limit,
                find
            })

            return await this._parseRes(res);
        },

        update: async (data) => {
            const res = await this._sendReq('/users', data, 'PATCH');

            return this._checkError(res);
        },
        updatePassword: async (oldP, newP) => {
            const res = await this._sendReq('/users/updatePassword', {
                oldPassword: oldP,
                newPassword: newP
            }, 'PATCH');

            return this._checkError(res);
        }
    }
}