import React from 'react';
import { connect } from 'react-redux';

import './style.scss';

import actions from '../../actions';

import { withStorytellerService } from '../hoc';

import { withRouter } from 'react-router-dom';
import { useState } from 'react';

import StoriesList from '../stories/stories';
import { useEffect } from 'react';
import { useCallback } from 'react';
import UserAvatar from '../user-avatar';

import ContentEditable from 'react-contenteditable';

const UserPage = ({ storytellerService, match, userData: myData, addErrorBanner, needLogin, location: { pathname }, history, setNextRoute }) => {
    const [storiesList, setStoriesList] = useState([]);
    const [userData, setUserData] = useState({});
    const [showAbout, setShowAbout] = useState(false);
    const [bigAbout, setBigAbout] = useState(false);
    const [aboutBlock, setAboutBlockRef] = useState(null);

    const [imFollower, setImFollower] = useState(false);
    const [followers, setFollowers] = useState([]);

    const [myPage, setMyPage] = useState(false);
    // const [myDrafts, setMyDraftsList] = useState([]); todo drafts


    const { params: { id: userId } } = match;
    const { username, name, surname, urlToImage, _id, about } = userData;
    const { userId: myId, username: myUsername } = myData;

    const getStories = useCallback(() => {
        if (_id) {
            let query = {
                find: {},
                selection: {}
            }

            query.find = {
                userId: _id
            }

            query.selection = {
                publishedAt: true,
                views: true,
                'comments._id': true,
                'likes._id': true,
                _id: true,
                previewImage: true,
                previewTitle: true,
                previewBody: true
            }

            storytellerService.stories.getAll(query)
                .then(storiesList => {
                    setStoriesList(storiesList)
                })
                .catch(() => { })
        }
    }, [storytellerService.stories, _id]);

    const getUserData = useCallback(() => {
        storytellerService.users.get(userId)
            .then(userData => {
                setUserData({
                    ...userData
                })
                setFollowers(userData.followers);
            })
            .catch(() => {
                addErrorBanner('   , -,      ', 3);
            })
    }, [storytellerService.users, userId, addErrorBanner])

    useEffect(() => {
        getUserData();
        getStories();
    }, [getUserData, getStories])

    useEffect(() => {
        if (aboutBlock) {
            if (aboutBlock.clientHeight > 210) {
                setBigAbout(true);
            }
        }
    }, [aboutBlock, userData])

    useEffect(() => {
        if (myId) {
            const _idx = followers.findIndex(el => el.userId === myId);

            if (_idx !== -1) {
                setImFollower(true);
            }
        }
    }, [followers, myId]);

    useEffect(() => {
        document.title = `${[name, surname].join(' ')}${name ? ' ' : ''}@${username || ''}`;
    }, [username, name, surname])

    useEffect(() => {
        if (myUsername === userId) {
            setMyPage(true);
        }
        else {
            setMyPage(false);
        }
    }, [userId, myUsername])

    const follow = () => {
        if (needLogin) {
            setNextRoute(pathname);
            history.push('/registration');
        }
        else {
            if (myId === _id) {
                addErrorBanner('     ', 3)
            }
            else {
                let followFunc = storytellerService.users;

                if (!imFollower) {
                    followFunc = followFunc.follow;
                    setImFollower(true);
                }

                else {
                    followFunc = followFunc.unfollow;
                    setImFollower(false);
                }

                followFunc(_id)
                    .then(() => {
                        getUserFollowers();
                    })
                    .catch(() => {
                        addErrorBanner('     ', 3);
                    })
            }
        }
    }

    const getUserFollowers = () => {
        const selection = {
            followers: true
        }

        storytellerService.users.getSmallData(userId, selection)
            .then(({ followers }) => {
                setFollowers(followers);
            })
            .catch(() => {

            })
    }

    const openAboutUser = () => {
        if (bigAbout) {
            setShowAbout(!showAbout);
        }
    }

    const openAboutFromText = () => {
        if (showAbout) {
            openAboutUser();
        }
    }

    return (
        <div className='user-page'>
            <div className="user-panel-container">
                <div className="top">
                    <UserAvatar className='user-avatar' userId={username} urlToImage={urlToImage} />
                    <div className="right">
                        <div className="user-names">
                            <div className="name-and-surname">
                                <span>
                                    {name}
                                </span>
                                <span>
                                    {surname}
                                </span>
                            </div>
                            <div className="username">
                                <span>
                                    @{username}
                                </span>
                            </div>
                        </div>
                        <div className="followers-panel">
                            {myPage ? null : <div className={`follow-btn ${imFollower ? 'unfollow' : ''}`} onClick={follow}>
                                <span>
                                    {
                                        imFollower ? '' : ''
                                    }
                                </span>
                            </div>}
                            <div className="followers-panel-inner">
                                <div className="followers-list">
                                    <div className='logo'></div>
                                    <div className='followers-list-inner'>
                                        <span className='number'>
                                            {followers.length.toLocaleString('en')}
                                        </span>
                                        <span>
                                            
                                        </span>
                                    </div>
                                </div>
                                <div className="stories-list-length">
                                    <div className='logo'></div>
                                    <div className='stories-list-inner'>
                                        <span>
                                            :
                                        </span>
                                        <span className='number'>
                                            {storiesList.length}
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                <div className="bottom">
                    {
                        about ?
                            <div className={`about-user ${showAbout ? 'open' : ''} ${bigAbout ? '' : 'hidden'}`} ref={ref => setAboutBlockRef(ref)}
                                onClick={openAboutFromText}>
                                <ContentEditable html={about} tagName='p' className='about-user-text' disabled />
                                <div className={`all-about-user-btn ${showAbout ? 'open' : ''}`}
                                    onClick={openAboutUser}>
                                    <span>
                                         
                                    </span>
                                </div>
                            </div> : null
                    }
                </div>
            </div>
            <StoriesList storiesList={storiesList} />
            {/* <FollowersList /> */}
        </div>
    )
}

const mapStateToProps = ({ userData, needLogin }) => ({
    userData,
    needLogin
})

export default connect(mapStateToProps, actions)(
    withStorytellerService()(
        withRouter(UserPage)
    )
)