import React, { useState, useEffect, useCallback } from 'react';

import TextField from './text-field';
import QuoteField from './quote-field';
import ImageField from './image-field';
import Distributor from './distributor';

export default ({ fiedls, removeFieldFunc, moveFieldFunc }) => {
    const [storiesBlocks, setStoriesBlocks] = useState([]);

    const createStoriesBlocks = useCallback((fields) => fields.map(field => {
        const { id, type } = field;

        if (type === 'text') return (
            <TextField key={id} id={id} deleteField={removeFieldFunc} moveField={moveFieldFunc}/>
        )

        else if (type === 'quote') return (
            <QuoteField key={id} id={id} deleteField={removeFieldFunc} moveField={moveFieldFunc} />
        )

        else if (type === 'distributor') return (
            <Distributor key={id} id={id} deleteField={removeFieldFunc} moveField={moveFieldFunc} />
        )

        else {
            const url = type.split(' ')[1];
            return (
                <ImageField key={id} id={id} deleteField={removeFieldFunc} moveField={moveFieldFunc} urlToImage={url}/>
            )
        }
    }), [moveFieldFunc, removeFieldFunc])

    useEffect(() => {
        setStoriesBlocks(
            createStoriesBlocks(fiedls)
        )
    }, [createStoriesBlocks, fiedls]);

    return (
        <div className='body'>
            {storiesBlocks}
        </div>
    )
}