import React from 'react';

export default class GenresList extends React.Component {
    input = React.createRef();

    state = {
        inputV: '',
        usedGenres: [],
        foundedGenres: []
    }

    handleChange = ({ target: { value } }) => {
        this.setState({
            inputV: value
        })
        this.createFoundedGenresList(value);
    }

    createUsedGenresList = () => {
        const { usedGenres, removeGenre } = this.props;

        if (usedGenres) {
            return usedGenres.map(genre => {

                return (
                    <li key={genre} onClick={() => removeGenre(genre)} className='used-genre'>
                        <span>
                            {genre}
                        </span>
                        <div className='cross' onClick={() => removeGenre(genre)}>

                        </div>
                    </li>
                )
            })
        }
        return null;
    }

    componentDidUpdate = ({ usedGenres: o }) => {
        const { usedGenres: n } = this.props;
        const { inputV } = this.state;

        if (o !== n && inputV !== 0) {
            this.createFoundedGenresList(inputV);
        }
    }

    handleKeyDown = ({ key }) => {
        if (key === 'Backspace') {
            const { removeLastUsedGenre, usedGenres } = this.props;
            const { inputV } = this.state;

            if (usedGenres.length > 0 && inputV === '') {
                removeLastUsedGenre();
            }
        }
    }

    createFoundedGenresList = (value) => {
        const { genresList, usedGenres } = this.props;

        let includeValueArr = [];

        genresList.forEach((genre) => {
            const { name } = genre;

            if (
                name.toLowerCase().includes(
                    value.toLowerCase()
                ) && value !== ''
            ) {
                includeValueArr.push(name);
            }
        })

        usedGenres.forEach(g => {
            includeValueArr.forEach((i, id) => {
                if (i === g) {
                    includeValueArr.splice(id, 1);
                }
            })
        })

        if (includeValueArr.length > 0) {
            const { addGenre } = this.props;

            this.setState({
                foundedGenres: includeValueArr.slice(0, 5).map((name) => {
                    return (
                        <li key={name} onClick={() => {
                            addGenre(name);
                            this.setState({
                                inputV: '',
                                foundedGenres: []
                            })
                        }}  className='found-genre'>
                            <div>
                                <span>
                                    {name}
                                </span>
                            </div>
                        </li>
                    )
                })
            })
        }
        else {
            this.setState({
                foundedGenres: []
            })
        }
    }

    handleFocus = () => {
        const { input } = this;

        if (input) {
            input.current.focus()
        }
    }

    handleMouseDown = ({path}) => {
        let con = true;
        
        for (let i = 0; i<path.length - 2; i++) {
            if ( path[i].classList.contains('genres') ) {
                con = false;
                break;
            }
        }

        if (con) {
            this.setState({
                inputV: '',
                foundedGenres: []
            })
        }

        document.removeEventListener('mouseup', this.handleMouseDown, false);
        document.removeEventListener('keydown', this.handleKeyDown, false);
    }

    componentWillUnmount = () => {
        document.removeEventListener('mouseup', this.handleMouseDown, false);
        document.removeEventListener('keydown', this.handleKeyDown, false);
    }

    render = () => {
        const { inputV, foundedGenres } = this.state;

        const usedGenresMappedList = this.createUsedGenresList();

        return (
            <div
                className='genres'
                onClick={this.handleFocus}
            >
                <ul className='used-genres' onClick={this.handleFocus}>
                    {usedGenresMappedList}
                    <li>
                        <input
                            value={inputV}
                            onChange={this.handleChange}
                            placeholder=" ..."
                            ref={this.input}
                            onFocus={() => {
                                document.addEventListener('keydown', this.handleKeyDown, false);
                            }}
                            onBlur={() => {
                                document.addEventListener('keydown', this.handleKeyDown, false);
                                document.addEventListener('mouseup', this.handleMouseDown, false);
                            }}
                        />
                    </li>
                </ul>
                <ul className={`founded-genres gnrs ${foundedGenres.length > 0 ? 'active' : 'disabled'}`}>
                    {foundedGenres}
                </ul>
            </div>
        )
    }
}