import React from 'react';
import { withStorytellerService } from '../hoc';
import { connect } from 'react-redux';
import actions from '../../actions';

import AuthorBlock from './user-block';

import { SimpleLoader } from '../loader';

import './style.scss';

const NoResultH1 = () => {
    return (
        <h3 className='find-nothing'>
                 
        </h3>
    )
}

class Authors extends React.Component {

    input = React.createRef();

    state = {
        authorsList: [],
        search: '',
        loading: true
    }

    createAuthorsList = (query) => {
        const { storytellerService, addErrorBanner } = this.props;

        let find = storytellerService.users.getAll;

        const skip = 0;
        const limit = 20;

        if (query) {
            find = find(skip, limit, {}, query);
        }
        else {
            find = find(skip, limit);
        }

        find.then((users) => {
            this.createAuthorBlocks(users);
        }).catch(() => {
            addErrorBanner('   , -, ', 5, 'white');
        })
    }

    createAuthorBlocks = (users) => {
        const newAuthorsList = users.map(user => {
            const { urlToImage, username, name, surname, _id, followers } = user;

            return (
                <AuthorBlock
                    key={_id}
                    urlToImage={urlToImage}
                    name={name}
                    surname={surname}
                    username={username}
                    followers={followers} />
            )
        })

        this.setState(({ authorsList }) => {
            // return {
            //     authorsList: [
            //         ...authorsList,
            //         ...newAuthorsList
            //     ]
            // }
            return {
                authorsList: newAuthorsList,
                loading: false
            }
        });
    }

    handleSearch = ({ target: { value } }) => {
        this.setState({
            search: value
        })
    }

    handleFocus = () => {
        this.input.current.focus();
    }

    createFilter = () => {
        const { search } = this.state;

        let find = {
            
        };

        if (search.length > 0) {
            find.username = { '$regex': `.*${search}.*` }
        }

        this.setState({
            authorsList: [],
            loading: true
        })

        this.createAuthorsList(find);
    }

    onKeyDown = ({ key }) => {
        if (key === 'Enter') {
            this.createFilter();
        }
    }

    componentDidMount = () => {
        document.title = '  Storyteller';

        this.createAuthorsList();

        document.addEventListener('keydown', this.onKeyDown, false);
    }

    componentWillUnmount = () => {
        document.removeEventListener('keydown', this.onKeyDown, false);
    }

    render() {
        const { authorsList, search, loading } = this.state;

        let showNothing = false;

        if (!loading && authorsList.length === 0) {
            showNothing = true;
        }

        return (
            <div className='authors'>
                <div className="filter">
                    <div className="search" onClick={this.handleFocus}>
                        <div className='icon' onClick={this.handleFocus}></div>
                        <input type="text" value={search} placeholder=" ( ' )" onChange={this.handleSearch} ref={this.input} />
                    </div>
                    <div className='blue-btn small' onClick={this.createFilter}>
                        <button>
                            <span>
                                
                            </span>
                        </button>
                    </div>
                </div>
                {
                    loading ? <SimpleLoader /> :
                    showNothing ?
                        <NoResultH1 /> :
                        authorsList
                }
            </div>
        )
    }
}

const mapStateToProps = () => {
    return {};
}

export default connect(mapStateToProps, actions)(
    withStorytellerService()(Authors)
);