import React from 'react';
import './style.scss';

import TitleField from './title-field';
import Body from './body';
import AddFieldButtonsGround from './add-field-buttons-group';

export default class StoryEditor extends React.Component {

    state = {
        bodyField: [
            {
                type: 'text',
                id: 0
            }
        ]
    }

    generateId = () => Date.now().toString() + Math.random();

    addField = (type) => this.setState(({ bodyField }) => {
        return {
            bodyField: [
                ...bodyField,
                {
                    type,
                    id: this.generateId()
                }
            ]
        }
    })

    removeField = (fieldId) => {
        const { bodyField } = this.state;
        const id = bodyField.findIndex(field => field.id === fieldId);

        let newBodyFields = bodyField.slice(0, bodyField.length);
        newBodyFields.splice(id, 1);
        this.setState({
            bodyField: newBodyFields
        })
    }

    moveField = (f, l) => {
        const { bodyField } = this.state;

        let a = bodyField.slice(0, bodyField.length);
        a.splice(f, 1);

        a.splice(l, 0, bodyField[f]);

        this.setState({bodyField: a});
    }

    render = () => {
        const { bodyField } = this.state;

        return (
            <div className='story-editor'>
                <TitleField />
                <Body fiedls={bodyField} removeFieldFunc={this.removeField} moveFieldFunc={this.moveField} />
                <AddFieldButtonsGround addField={this.addField} />
            </div>
        )
    }
}