import React from 'react';


import { connect } from 'react-redux';
import actions from '../../actions';

import Input from '../input';

import UserAvatar from './user-avatar';

import { withStorytellerService } from '../hoc';

class ProfileEdit extends React.Component {
    imageForm = React.createRef();

    state = {
        username: '',
        name: '',
        surname: '',
        about: '',
        email: '',

        loadMyData: true,

        trueEmail: true,
        trueUser: true,

        usernameTimer: null,

        updateButtonEnable: false
    }

    componentDidUpdate = () => {
        const { trueUser, updateButtonEnable, trueEmail } = this.state;

        this.loadMyData();

        if (trueEmail && trueUser) {
            if (!updateButtonEnable) {
                this.setState({
                    updateButtonEnable: true
                })
            }
        }
        else {
            if (updateButtonEnable) {
                this.setState({
                    updateButtonEnable: false
                })
            }
        }
    }

    loadMyData = () => {
        const { loadMyData } = this.state;
        const { userData } = this.props;

        const { username = '', email = '', name = '', surname = '', about = '' } = userData;

        if (loadMyData && username) {

            this.setState({
                username,
                email,
                name,
                surname,
                about,
                loadMyData: false
            })
        }
    }

    componentDidMount = () => {
        this.loadMyData();
    }

    clickOnImageForm = () => {
        this.imageForm.current.click();
    }

    uploadImage = (evt) => {
        const { storytellerService, addErrorBanner } = this.props;

        storytellerService.image.post(evt.target.files[0])
            .then(urlToImage => {
                storytellerService.users.update({
                    urlToImage: `${storytellerService.image.get(urlToImage)}`
                })
                    .then(() => {
                        this.loadSmallData({
                            urlToImage: true
                        });
                    })
                    .catch(() => {
                        this.serverError();
                    })
            })
            .catch((status) => {
                if (status === 400) {
                    addErrorBanner('     png, jpg, jpeg')
                }
                else {
                    this.serverError();
                }
            });

        evt.target.value = null;
    }

    loadSmallData = (selection, uname) => {
        const { storytellerService, userData, updateUserData, addErrorBanner } = this.props;
        const { username } = userData;

        storytellerService.users.getSmallData(uname || username, selection)
            .then(userData => {
                updateUserData({
                    ...userData
                })

                addErrorBanner('', 3);
            })
            .catch(() => {
                console.log('error');
            })
    }

    updateUserData = () => {
        const { storytellerService, userData } = this.props;
        const {username, email, name, surname, about} = this.state;

        const {username: myUsername, email: myEmail, name: myName, surname: MySurname, about: AboutMe} = userData;

        let selection = {};

        if (username !== myUsername) {
            selection.username = username;
        }
        if (email !== myEmail) {
            selection.email = email;
        }
        if (name !== myName) {
            selection.name = name;
        }
        if (surname !== MySurname) {
            selection.surname = surname;
        }
        if (about !== AboutMe) {
            selection.about = about;
        }

        storytellerService.users.update(selection)
            .then(() => {
                this.loadSmallData(selection, username !== myUsername ? username : undefined);
            })
            .catch(() => {
                this.serverError();
            })
    }

    handleClick = () => {
        this.updateUserData();
    }

    serverError = () => {
        const { addErrorBanner } = this.props;

        addErrorBanner('   , -, ', 3);
    }

    usernameU = ({ target: { value } }) => {
        this.setState({
            username: this.correctUsername(value)
        })
    }

    emailU = ({ target: { value } }) => this.setState({
        email: this.correctEmail(value)
    })

    correctUsername = (value) => {
        const { addErrorBanner, storytellerService, userData: {username} } = this.props;

        storytellerService.checkUsername(value)
            .then(code => {
                const clearTimer = () => {
                    const { usernameTimer } = this.state;
                    clearTimeout(usernameTimer);
                }

                const addTimer = (message, delayTime) => {
                    clearTimer();

                    this.setState({
                        usernameTimer: setTimeout(() => {
                            addErrorBanner(message, delayTime, 'white');
                        }, 500),
                        trueUser: false
                    })
                }

                if (value.length !== 0) {
                    switch (code) {
                        case 'busy':
                            if (value === username) {
                                this.setState({
                                    trueUser: true
                                })
                            }
                            else {
                                addTimer('    ', 3);
                            }
                            break;

                        case 'incorrect':
                            addTimer('   4  16 .    ,     "_"', 3);
                            break;

                        default:
                            clearTimer();
                            this.setState({
                                trueUser: true
                            })
                    }
                }
            })
            .catch(() => {
                addErrorBanner('   , -, ', 3, 'white');
            })

        return value;
    }

    correctEmail = (value) => {
        const { addErrorBanner, storytellerService, userData: {email} } = this.props;

        storytellerService.checkEmail(value)
            .then(code => {
                const clearTimer = () => {
                    const { usernameTimer } = this.state;
                    clearTimeout(usernameTimer);
                }

                const addTimer = (message, delayTime) => {
                    clearTimer();

                    this.setState({
                        usernameTimer: setTimeout(() => {
                            addErrorBanner(message, delayTime, 'white');
                        }, 2500),
                        trueEmail: false
                    })
                }

                if (value.length !== 0) {
                    switch (code) {
                        case 'busy':
                            if (value === email) {
                                this.setState({
                                    trueEmail: true
                                })
                            }
                            else {
                                addTimer('   ', 3);
                            }
                            break;

                        case 'incorrect':
                            addTimer('  ', 3);
                            break;

                        default:
                            clearTimer();
                            this.setState({
                                trueEmail: true
                            })
                    }
                }
            })
            .catch(() => {
                addErrorBanner('   , -, ', 3, 'white');
            })

        return value;
    }

    nameU = ({ target: { value } }) => {
        this.setState({
            name: value
        })
    }

    surnameU = ({ target: { value } }) => {
        this.setState({
            surname: value
        })
    }

    aboutU = ({ target: { value } }) => {
        this.setState({
            about: value
        })
    }

    render() {
        const { userData: { urlToImage } } = this.props;
        const { username, name, surname, email, about, updateButtonEnable } = this.state;

        return (
            <div className='profile-edit setting-inner'>
                <div className="user-preview">
                    <div className="user-avatar-container">
                        <UserAvatar className='user-avatar' urlToImage={urlToImage} onClick={this.clickOnImageForm} />
                    </div>
                    <div className="right">
                        <div className='username'>
                            <span>
                                @{username}
                            </span>
                        </div>
                        <button className='change-my-avatar' onClick={this.clickOnImageForm}>
                              
                        </button>
                    </div>
                    <input type="file" ref={this.imageForm} onChange={this.uploadImage} />
                </div>
                <div className="user-data">
                    <div className="username small-data">
                        <aside>
                            
                        </aside>
                        <input type="text" placeholder={'...'} value={username} onChange={this.usernameU} maxLength='16'/>
                    </div>
                    <div className="name small-data">
                        <aside>
                            '
                        </aside>
                        <input type="text" placeholder={`...`} value={name} onChange={this.nameU} />
                    </div>
                    <div className="surname small-data">
                        <aside>
                            
                        </aside>
                        <input type="text" placeholder={'...'} value={surname} onChange={this.surnameU} />
                    </div>
                    <div className="email small-data">
                        <aside>
                            
                        </aside>
                        <input type="text" placeholder={'...'} value={email} onChange={this.emailU} />
                    </div>
                    <div className="about small-data">
                        <aside>
                             
                        </aside>
                        <Input placeholder='...' disabledAutoFocus tagName='span' value={about} onChange={this.aboutU} />
                    </div>
                    <div className="load-my-data small-data" >
                        <div className={`blue-btn small ${updateButtonEnable ? '' : 'disabled'}`}>
                            <button onClick={this.handleClick}>
                                <span>
                                    
                                </span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default connect(({ userData }) => ({ userData }), actions)(
    withStorytellerService()(ProfileEdit)
);