import React from 'react';

import { Route, Switch, withRouter } from 'react-router-dom';

import './style.scss';

import ProfileEdit from './profile-edit';

import ChangeSettingBtn from './change-setting-btn';
import ChangePassword from './change-password';

import MyStories from './stories';

import UserFollowers from './followings';

import {withStorytellerService} from '../hoc';
import { connect } from 'react-redux';
import actions from '../../actions';


class MySettings extends React.Component {

    linkTo = (to) => {
        const { history } = this.props;
        history.push(`/settings${to}`);
    }

    logout = () => {
        const {storytellerService, login, userData, history} = this.props;

        storytellerService.logout();
        login(true);

        history.push(`/authors/${userData.username}`);
    }

    render() {
        const { location } = this.props;

        return (
            <div className='my-settings'>
                <div className="settings-panel">
                    <div className="settings-panel-inner">
                        <ChangeSettingBtn linkTo={this.linkTo} link='/profile' location={location}>
                             
                        </ChangeSettingBtn>
                        <ChangeSettingBtn linkTo={this.linkTo} link='/password' location={location}>
                             
                        </ChangeSettingBtn>
                        <ChangeSettingBtn linkTo={this.linkTo} link='/stories' location={location}>
                            
                        </ChangeSettingBtn>
                        <ChangeSettingBtn linkTo={this.linkTo} link='/followings' location={location}>
                             ...
                        </ChangeSettingBtn>
                        <button className='logout' onClick={this.logout}>
                            
                        </button>
                    </div>
                </div>
                <div className='setting'>
                    <Switch>
                        <Route path='/settings/profile'>
                            <ProfileEdit />
                        </Route>
                        <Route path='/settings/password'>
                            <ChangePassword />
                        </Route>
                        <Route path='/settings/stories'>
                            <MyStories />
                        </Route>
                        <Route path='/settings/followings'>
                            <UserFollowers />
                        </Route>
                    </Switch>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.userData
    }
}

export default connect(mapStateToProps, actions)(
    withStorytellerService()(
        withRouter(MySettings)
    )
)