import React from 'react';
import { withStorytellerService } from '../hoc';

import './style.scss';
import { withRouter } from 'react-router-dom';

import Comment from './comment';

import StoryBlock from './story-block';

import { Link } from 'react-router-dom';
import MyComment from './my-comment';

import { connect } from 'react-redux';

import actions from '../../actions';

class StoryPage extends React.Component {

    state = {
        title: null,
        body: null,
        date: null,
        comments: [],
        likes: [],
        author: null,
        views: 0,
        quote: null,
        userId: null,
        liked: false,
        checkMyLike: true,
        storyId: null,
        username: null,

        commentBlocks: []
    }

    createTitleBlock = (title) =>
        <StoryBlock className='title' tagName='h3'>
            {title}
        </StoryBlock>;

    createBodyBlocks = (bodyBlocks) => bodyBlocks.map((block, id) => {
        const { type, value } = block;

        let sb = null;

        if (type === 'text') {
            sb = <StoryBlock tagName='p' className={type} key={id}>{value}</StoryBlock>
        }
        else if (type === 'quote') {
            sb = <StoryBlock tagName='blockquote' className={type} key={id}>{value}</StoryBlock>

            if (id === 0) {
                this.setState(() => ({ quote: sb }));
                sb = null;
            }
        }
        else if (type === 'hr') {
            sb = <div className='hr' key={id}></div>
        }
        else if (type === 'img') {
            sb = <img src={value} alt="" key={id} />
        }

        return sb;
    })

    createBeautyQuote = (quote, author) => {
        const { username } = this.state;
        if (quote && author) {
            return (
                <div className='beauty-quote'>
                    {quote}
                    <Link to={`/authors/${username}`}>
                        <span className='author'>
                            {author}
                        </span>
                        <span className='username'>
                            @{username}
                        </span>
                    </Link>
                </div>
            )
        }
        return null;
    }

    loadStory = (storyData) => {
        const { title, body, publishedAt, userId, likes, comments, views, previewTitle } = storyData;
        document.title = previewTitle;
        const { storytellerService } = this.props;

        const bodyBlocks = this.createBodyBlocks(body);

        this.setState(() => ({
            title: this.createTitleBlock(title),
            body: bodyBlocks,
            date: publishedAt,
            views: views,
            likes: likes,
            comments: comments,
            userId
        }))

        this.createComments(comments);

        const selection = {
            username: true,
            name: true,
            surname: true
        }

        storytellerService.users.getById(userId, selection).then(({ username, name, surname }) => {
            let author = null;

            if (name && surname) {
                author = name + ' ' + surname;
            }

            this.setState(() => ({ author, username }))
        })
    }

    createComments = (comments) => {
        const { storytellerService, match: { params: { id: storyId } } } = this.props;

        const coms = comments.reverse().map(({ _id, publishedAt, body, userId, likes }) => {
            return <Comment
                body={body}
                publishedAt={storytellerService.convertData(publishedAt)}
                userId={userId}
                likes={likes}
                key={_id}
                storyId={storyId}
                id={_id}
                updateCommentsList={this.loadComments}
            />
        })

        this.setState({
            commentBlocks: coms
        })
    }

    loadStoryDataOnService = (id) => {
        const { storytellerService } = this.props;

        storytellerService.stories.get(id)
            .then(this.loadStory)
            .catch(() => { })
    }

    componentDidMount = () => {
        const { match: { params: { id } } } = this.props;

        this.loadStoryDataOnService(id);

        this.setState({ storyId: id })
    }

    componentDidUpdate = () => {
        const { userId } = this.props.userData;
        const { likes, checkMyLike } = this.state;

        if (userId && likes.length > 0 && checkMyLike) {
            const myLike = likes.findIndex(like => like.userId === userId);

            if (myLike !== -1) {
                this.setState({
                    liked: true,
                    checkMyLike: false
                })
            }
            else {
                this.setState({
                    liked: false,
                    checkMyLike: false
                })
            }
        }
    }

    like = () => {
        const { match: { params: { id } }, storytellerService, addErrorBanner, needLogin, setNextRoute, location: { pathname }, history } = this.props;
        const { liked } = this.state;

        if (needLogin) {
            setNextRoute(pathname);
            history.push('/registration');
        }
        else {
            let likeFunc = storytellerService.stories;

            if (liked) {
                likeFunc = likeFunc.unlike;
            }
            else {
                likeFunc = likeFunc.like;
            }

            this.setState(({ liked }) => ({ liked: !liked }));

            likeFunc(id)
                .then(() => {
                    this.loadLikes();
                })
                .catch(() => {
                    addErrorBanner('     ', 3);
                })
        }
    }

    loadLikes = () => {
        const { storytellerService, addErrorBanner } = this.props;
        const { storyId } = this.state;

        const selection = {
            "likes": true
        }

        storytellerService.stories.getSmallData(storyId, selection)
            .then(({ likes }) => {
                this.setState({ likes });
            })
            .catch(() => {
                addErrorBanner('     ', 3);
            })
    }

    loadComments = () => {
        const { storytellerService, addErrorBanner } = this.props;
        const { storyId } = this.state;

        const selection = {
            "comments": true
        }

        storytellerService.stories.getSmallData(storyId, selection)
            .then(({ comments }) => {
                this.setState({ comments });
                this.createComments(comments);
            })
            .catch(() => {
                addErrorBanner('     ', 3);
            })
    }

    render() {
        const { title, body, date, comments, likes, author, views, quote, username, liked, storyId, commentBlocks } = this.state;
        const { needLogin } = this.props;

        const beautyQuote = this.createBeautyQuote(quote, author);
        let absAuthor = null;
        if (!beautyQuote && username) {
            absAuthor =
                <div className='author-container'>
                    <Link to={`/authors/${username}`}>
                        <span className='author'>
                            {author}
                        </span>
                        <span className='username'>
                            @{username}
                        </span>
                    </Link>
                </div>
        }

        return (
            <div className='story-page'>
                <div className='story'>
                    {title}
                    {absAuthor}
                    {beautyQuote}
                    {body}
                    <h3 className='-- to-be-continued ->>'> </h3>
                </div>
                <div className="bottom">
                    <div className="left">
                        <div className={`likes num ${liked ? 'active' : ''}`} onClick={this.like}><span>
                            {likes.length.toLocaleString('en')}</span>
                        </div>
                        <div className="comments num"><span>{comments.length.toLocaleString('en')}</span></div>
                        <div className="views num"><span>{views.toLocaleString('en')}</span></div>
                    </div>
                    <div className="right">
                        <div className="publishedAt">
                            {
                                date ? (
                                    <React.Fragment>
                                        <span>{date.day} </span>
                                        <span>{date.month} </span>
                                        <span>{date.year}</span>
                                    </React.Fragment>
                                ) : null
                            }
                        </div>
                    </div>
                </div>
                <div className='comments'>
                    <div className="user-comments">
                        {
                            needLogin ? (
                                <div className='need-login'>
                                    <span>
                                        ,  
                                    </span>
                                </div>
                            ) : <MyComment storyId={storyId} loadComments={this.loadComments} />
                        }
                    </div>
                    <div className="comment-list">
                        {commentBlocks}
                    </div>
                </div>
            </div >
        )
    }
}

export default connect(({ userData, needLogin }) => ({ userData, needLogin }), actions)(
    withStorytellerService()(
        withRouter(StoryPage)
    )
)