import React, { useState, useEffect } from 'react';
import './style.scss';

import LinkButton from '../link-button';

import actions from '../../actions';
import { connect } from 'react-redux';

const WrStBtn = ({ setNextRoute }) => {

    const handleClick = () => {
        setNextRoute('/write-story');
    }

    return (
        <LinkButton text=" " linkTo="/write-story" className="blue-btn" onClick={handleClick} />
    )
}

const WriteStoryButton = connect(() => ({}), actions)(
    WrStBtn
)


const GenreButton = connect(() => ({}), actions)(
    ({ text, id, setFirstStoryGenresList }) => {

        const [active, setActive] = useState(false);

        const onClick = () => {
            setFirstStoryGenresList(text);
            setActive(!active);
        }

        return (
            <button className={active ? "active" : ""} onClick={onClick}><span>{text}</span></button >
        )
    }
);

const Genres = connect(({ genresList }) => ({ genresList }), actions)(
    ({ genresList }) => {
        const [genresButtonList, setGenresButtonList] = useState([]);

        useEffect(() => {
            setGenresButtonList(genresList.map(({ _id, name }) =>
                (<GenreButton text={name} key={_id} id={_id} />)
            ))
        }, [genresList])

        return (
            <div className="genres">
                <h1>    <br />  !</h1>
                <div className="genres-list">
                    {genresButtonList}
                </div>
                <h3>
                          ?
                </h3>
                <WriteStoryButton />
            </div>
        )
    }
);


class About extends React.Component {

    componentDidMount = () => {
        document.title = '   Storyteller';
    }

    render() {

        return (
            <div className="about">
                <Genres />
                <div className="main">
                    <section className='your-story'>
                        <div className="text">
                            <h2><br /> !</h2>
                            <p>
                                    
                                ?      ?
                    <span> !    , 
                       !</span>
                            </p>
                        </div>
                    </section>
                    <section className='reverse chapter'>
                        <div className="text">
                            <h2>  <br />  .</h2>
                            <p>
                                      ,
                                    , 
                     . <span>  
                        .</span>
                            </p>
                        </div>
                    </section>
                    <section className='feedback'>
                        <div className="text">
                            <h2> .</h2>
                            <p>
                                 ,   , 
                                ,    
                                   . 
                      ? !<span> 
                                   
                     .</span>
                            </p>
                        </div>
                    </section>
                </div>
                <div className="bottom">
                    <h2>  .<br />  .</h2>
                    <WriteStoryButton />
                </div>
            </div>
        )
    }
}

export default About;