import React from 'react';
import ContentEditable from 'react-contenteditable';

import './style.scss';

export default class extends React.Component {
    input = null;

    componentDidMount = () => {
        const { disabledAutoFocus } = this.props;

        if (!disabledAutoFocus) {
            this.input.el.current.focus();
        }
    }

    handlePaste = e => {
        const { onPaste } = this.props;

        e.preventDefault();

        if (onPaste) onPaste(e);
        else {
            const data = e.clipboardData.getData('Text');
            document.execCommand('insertText', false, data);
        }
    }

    render = () => {
        const { value = '',
            disabled = false,
            className = '',
            placeholder = null,
            onFocus = null,
            onBlur = null,
            onMouseEnter = null,
            onMouseLeave = null,
            onChange = null,
            tagName='div'
        } = this.props;

        return (
            <ContentEditable
                html={value}
                className={`input-contenteditable ${className}`}
                disabled={disabled}
                data-placeholder={placeholder}
                tagName={tagName}
                onChange={onChange}
                onBlur={onBlur}
                onFocus={onFocus}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
                onPaste={this.handlePaste}
                ref={ref => this.input = ref}
            >

            </ContentEditable>
        )
    }
}