import React, { useState } from 'react';

import Input from '../../input';

export default () => {
    const [value, setValue] = useState('');

    const handleChange = ({ target: { value } }) => setValue(value);

    return (
        <Input
            tagName='h3'
            placeholder='  !'
            value={value}
            className='title-field se-item'
            onChange={handleChange}
        />
    )
}