import React from 'react';

import { withStorytellerService } from '../hoc';

import { Switch, Route, Redirect, withRouter } from 'react-router-dom';

import { connect } from 'react-redux';
import actions from '../../actions';

import Header from '../header';
import Footer from '../footer';

import About from '../about';

import Registration from '../registration';
import Login from '../login';

import StoryEditor from '../story-editor';
import Stories from '../stories';

import StoryPage from '../story-page';

import UserPage from '../user-page';

import Authors from '../authors';

import MySettings from '../my-settings';

import ErrorBannerContainer from '../error-baner';
// import { StorytellerLoader } from '../loader';

class App extends React.Component {

    // state = {
    //     loading: true
    // }

    componentDidMount() {
        const { login, loadGenres, storytellerService, updateUserData, addErrorBanner } = this.props; //, history

        storytellerService.auth()
            .then((userData) => {
                const { status, username, name, surname, urlToImage, _id: userId, about, email } = userData;

                switch (status) {
                    case 401:
                        login(true);
                        // history.push('/about');
                        break;

                    case 200:
                        login(false);
                        updateUserData({
                            userId,
                            username,
                            name,
                            urlToImage,
                            surname,
                            about,
                            email
                        })
                        break;

                    default:
                        addErrorBanner('   , -, ', 3);
                }
                
                // this.setState({
                //     loading: false
                // })
            })

        storytellerService.genres.getAll().then(loadGenres);
    }

    render() {
        const { needLogin } = this.props;
        // const { loading } = this.state;

        return (
            // <React.Fragment>
            //     {loading ? <StorytellerLoader /> :
                    <React.Fragment>
                        <Header />
                        <main>
                            <Switch>
                                <Route path="/about">
                                    <About />
                                </Route>
                                <Route path="/registration">
                                    {
                                        needLogin ? <Registration /> : <Redirect to='/' />
                                    }
                                </Route>
                                <Route path="/login">
                                    {
                                        needLogin ? <Login /> : <Redirect to='/' />
                                    }
                                </Route>
                                <Route path="/write-story">
                                    {
                                        needLogin ? <Redirect to='/login' /> : <StoryEditor />
                                    }
                                </Route>
                                <Route path='/stories/' exact>
                                    <Stories />
                                </Route>
                                <Route path='/authors/' exact>
                                    <Authors />
                                </Route>
                                <Route path='/stories/:id'>
                                    <StoryPage />
                                </Route>
                                <Route path='/authors/:id'>
                                    <UserPage />
                                </Route>
                                <Route path='/settings'>
                                    {
                                        needLogin ? <Redirect to='/login' /> : <MySettings />
                                    }
                                </Route>
                                <Route path="/">
                                    {
                                        needLogin ? <Redirect to='/about' /> : <Stories />
                                    }
                                </Route>
                            </Switch> 

                            {/* <Switch>
                                <Route path="/about">
                                    <About />
                                </Route>
                                <Route path="/registration">
                                    {
                                        needLogin ? <Registration /> : <Redirect to='/' />
                                    }
                                </Route>
                                <Route path="/login">
                                    {
                                        needLogin ? <Login /> : <Redirect to='/' />
                                    }
                                </Route>
                                <Route path="/write-story">
                                    <StoryEditor />
                                </Route>
                                <Route path='/stories/' exact>
                                    <Stories />
                                </Route>
                                <Route path='/authors/' exact>
                                    <Authors />
                                </Route>
                                <Route path='/stories/:id'>
                                    <StoryPage />
                                </Route>
                                <Route path='/authors/:id'>
                                    <UserPage />
                                </Route>
                                <Route path='/settings'>
                                    <MySettings />
                                </Route>
                                <Route path="/">
                                    <Stories />
                                </Route>
                            </Switch> */}
                        </main>
                        <Footer />
                        <ErrorBannerContainer />
                    </React.Fragment>
            //         }
            // </React.Fragment>
        )
    }
}

export default connect((state) => ({ needLogin: state.needLogin }), actions)(
    withStorytellerService()(
        withRouter(App)
    )
);