import React from 'react';
import { StorytellerServiceConsumer } from '../context/storyteller-service';

export default () => (Wrapped) => {

    return (props) => {
        return (
            <StorytellerServiceConsumer>
                {
                    (storytellerService) => {
                        return (<Wrapped {...props} storytellerService={storytellerService} />)
                    }
                }
            </StorytellerServiceConsumer>
        )
    }
}