import React from 'react';
import LinkButton from '../link-button';

import LastChange from './last-change';
import { useState } from 'react';
import { useEffect } from 'react';

export default () => {
    const [click, setClick] = useState(false);

    const [storyData, setStoryData]  = useState({
        title: '',
        body: []
    })

    const handleClick = () => {
        setClick(true);
        getStory();
        
        document.removeEventListener('keydown', handleKeyDown, false);
        document.addEventListener('keydown', handleKeyDown, false);
    }    

    const handleKeyDown = ({key}) => {
        if (key === 'Escape') {
            document.removeEventListener('keydown', handleKeyDown, false);
            setClick(false);
        }
    }

    const getStory = () => {
        const title = document.querySelector('.story-editor .title-field').innerHTML;

        const bodyItems = Array.prototype.slice.call(document.querySelectorAll('.story-editor .body .stories-block .story-item'));

        const body = bodyItems.map(item => {
            let itemData = {
                type: '',
                value: item.innerHTML
            }

            if (item.classList.contains('text')) {
                itemData.type = 'text';
            }
            if (item.classList.contains('quote')) {
                itemData.type = 'quote';
            }
            if (item.classList.contains('img')) {
                itemData.type = 'img';
                itemData.value = item.src;
            }
            if (item.classList.contains('distributor')) {
                itemData.type = 'hr';
                delete itemData.value;
            }

            return itemData;
        })
        setStoryData({
            title,
            body
        })
    }

    useEffect(() => {
        document.title = '   Storyteller'
    }, [])

    useEffect(() => {
        if (click) {
            document.body.style.overflowY = 'hidden';
        }
        else {
            document.body.style.overflowY = 'auto';
        }

        return () => {
            document.body.style.overflowY = 'auto';
        }
    }, [click])

    return (
        <React.Fragment>
            <LinkButton className="blue-btn minimize a" linkTo="#" text="" onClick={handleClick}/>
            {
                <LastChange className={click ? "" : "hidden"} remove={() => setClick(false)} title={storyData.title} body={storyData.body}/>
            }
        </React.Fragment>
    )
}