import React from 'react';
import { Link } from 'react-router-dom';
import './style.scss';

export default ({ text, linkTo, onClick, className, style={} }) => {

    return (
        <Link
            to={linkTo}
            className={className}
            style={style}
        >
            <button onClick={onClick}>
                <span>
                    {text}
                </span>
            </button>
        </Link>
    )
}