import React from 'react';

const UserAvatar = ({ className, urlToImage, onClick }) => {
    return (
        <div className={`${className} ${urlToImage ? '' : 'default'}`}
            style={
                urlToImage ? { backgroundImage: `url(${urlToImage})` } : {}
            }
            onClick={onClick}>

        </div>
    )
}

export default UserAvatar;