import React from 'react';

import './style.scss';

import { withStorytellerService } from '../hoc';

import Genres from '../push-story/genres';
import { connect } from 'react-redux';

import StoriesList from './stories';

import { SimpleLoader } from '../loader';

const NoResultH1 = () => {
    return (
        <h3 className='find-nothing'>
                 
        </h3>
    )
}

class Stories extends React.Component {
    input = React.createRef();

    state = {
        stories: [],
        search: '',
        usedGenres: [],
        sortBy: ' ',
        openSortByFilter: false,

        loading: false
    }

    createFilter = () => {
        const { usedGenres, search, sortBy } = this.state;

        let query = {
            find: {

            },
            selection: {

            }
        };

        switch (sortBy) {
            case ' ':
                query.sort = {
                    publishedAt: -1
                }
                break;

            case ' ':
                query.sort = {
                    views: -1,
                    likes: -1
                }
                break;

            default:
                break;
        }



        if (search.length > 0) {
            query.find.previewTitle = { '$regex': `.*${search}.*` }
        }

        if (usedGenres.length > 0) {
            query.where = {
                key: 'genres',
                value: usedGenres
            }
        }

        query.selection = {
            publishedAt: true,
            views: true,
            'comments._id': true,
            'likes._id': true,
            _id: true,
            previewImage: true,
            previewTitle: true,
            previewBody: true
        }

        this.setState({
            stories: [],
            loading: true
        })

        this.getStories(query);
    }

    getStories = (query) => {
        const { storytellerService } = this.props;

        storytellerService.stories.getAll(query)
            .then(storiesList => {

                this.setState({
                    stories: storiesList,
                    loading: false
                })
            })
            .catch(() => {
                this.setState({
                    stories: [],
                    loading: false
                })
            })
    }

    handleSearch = ({ target: { value } }) => {
        this.setState({
            search: value
        })
    }

    removeLastUsedGenre = () => {
        const { usedGenres } = this.state;
        console.log(123);
        let newArr = usedGenres.slice(0, usedGenres.length - 1);
        newArr.splice(newArr.length, 1);

        this.setState({
            usedGenres: newArr
        })
    }

    addGenre = (genre) => {

        const { usedGenres } = this.state;


        const idx = usedGenres.findIndex(g => g === genre);

        if (idx !== -1) {
            this.removeGenre(genre);
        }
        else {

            this.setState({
                usedGenres: [
                    ...usedGenres,
                    genre
                ]
            })
        }
    }

    removeGenre = (genre) => {
        this.setState(({ usedGenres }) => {
            const id = usedGenres.findIndex(g => g === genre);

            if (id === -1) {
                return {
                    usedGenres
                }
            }
            else {
                let newArr = usedGenres.slice(0, usedGenres.length);
                newArr.splice(id, 1);

                return {
                    usedGenres: newArr
                }
            }
        })
    }

    handleFocus = () => {
        this.input.current.focus();
    }

    changeSortByFilter = (value) => {
        this.setState({
            sortBy: value
        })
    }

    handleMouseDown = ({ path }) => {
        let con = true;

        for (let i = 0; i < path.length - 2; i++) {
            if (path[i].classList.contains('sort-by-filter')) {
                con = false;
                break;
            }
        }

        if (con) {
            this.setState({
                openSortByFilter: false
            })
        }

        document.removeEventListener('mouseup', this.handleMouseDown, false);
    }

    openSortByFilter = () => {
        this.setState(({ openSortByFilter }) => {
            if (!openSortByFilter) {
                document.addEventListener('mouseup', this.handleMouseDown, false);
            }

            return {
                openSortByFilter: !openSortByFilter
            }
        })
    }

    onKeyDown = ({ key }) => {
        if (key === 'Enter') {
            this.createFilter();
        }
    }

    componentDidMount = () => {
        document.title = '  Storyteller';
        this.createFilter();

        document.addEventListener('keydown', this.onKeyDown, false);
    }

    componentWillUnmount = () => {
        document.removeEventListener('mouseup', this.handleMouseDown, false);
        document.removeEventListener('keydown', this.onKeyDown, false);
    }

    render() {
        const { stories, search, usedGenres, sortBy, openSortByFilter, loading } = this.state;
        const { genresList } = this.props;

        let showNothing = false;

        if (!loading && stories.length === 0) {
            showNothing = true;
        }

        return (
            <div className="stories-page">
                <div className="filter">
                    <div className="search" onClick={this.handleFocus}>
                        <div className='icon' onClick={this.handleFocus}></div>
                        <input type="text" value={search} placeholder='' onChange={this.handleSearch} ref={this.input} />
                    </div>
                    <Genres
                        removeLastUsedGenre={this.removeLastUsedGenre}
                        usedGenres={usedGenres}
                        genresList={genresList}
                        addGenre={this.addGenre}
                        removeGenre={this.removeGenre}
                    />
                    <div className={`sort-by-filter ${openSortByFilter ? 'open' : ''}`} onClick={() => this.openSortByFilter()}>
                        <div className='sort-by-preview'>
                            <span>
                                {sortBy}
                            </span>
                        </div>
                        <div className='sort-by-filters'>
                            <div className='sort-by' onClick={() => this.changeSortByFilter(' ')}>
                                <span>
                                     
                                </span>
                            </div>
                            <div className='sort-by' onClick={() => this.changeSortByFilter(' ')}>
                                <span>
                                     
                                </span>
                            </div>
                        </div>
                    </div>
                    <div className='blue-btn small' onClick={this.createFilter}>
                        <button>
                            <span>
                                
                            </span>
                        </button>
                    </div>
                </div>
                {
                    loading ? <SimpleLoader /> :
                        showNothing ?
                            <NoResultH1 /> :
                            <StoriesList storiesList={stories} />
                }
            </div>
        )
    }
}

export default connect(({ genresList }) => ({ genresList }))(
    withStorytellerService()(Stories)
);