import React from 'react';
import './style.scss';

import { Link } from 'react-router-dom';

const Footer = () => {
    return (
        <footer>
            <div className='left'>
                <Link to="/">
                    <div className="storyteller-logo"></div>    
                </Link>
                <span className='copyright'> 2020, Storyteller</span>
            </div>
            <div className='right'>
                <li>
                    <Link to="/stories"><span></span></Link>
                </li>
                <li>
                    <Link to="/authors"><span></span></Link>
                </li>
                <li>
                    <Link to="/about"><span> Storyteller</span></Link>
                </li>
            </div>
        </footer>
    )
}

export default Footer;