import React from 'react';

import './style.scss';
import { useEffect } from 'react';
import { connect } from 'react-redux';

import actions from '../../actions';

import { withRouter } from 'react-router-dom';

import { withStorytellerService } from '../hoc';

const SettingsPanel = ({ visible, hideMe, history, login, storytellerService }) => {

    useEffect(() => {
        if (visible) {
            document.body.style.overflowY = 'hidden';
        }
        else {
            document.body.style.overflowY = 'auto';
        }
    }, [visible])

    const linkTo = (to) => {
        hideMe();
        history.push(`/settings${to}`);
    }

    const logout = () => {
        login(true);
        hideMe();
        storytellerService.logout();
    }

    return (
        <div className={`settings-panel-container ${visible ? '' : 'hide'}`}>
            <div className="background" onClick={hideMe}></div>
            <div className="settings-panel-inner">
                <button onClick={() => linkTo('/profile')}>
                     
                </button>
                <button onClick={() => linkTo('/password')}>
                     
                </button>
                <button onClick={() => linkTo('/stories')}>
                    
                </button>
                <button onClick={() => linkTo('/followings')}>
                     ...
                </button>
                <button className='logout' onClick={logout}>
                    
                </button>
                <button onClick={hideMe}>
                    
                </button>
            </div>
        </div>
    )
}

export default connect(() => ({}), actions)(
    withRouter(
        withStorytellerService()(SettingsPanel)
    )
);